  public static bmgfGetUnPost(psPoflg) {
    let sqalFilter = "'+CAST(COUNT(vouchno) as nvarchar(10))+'";
    let json = '{ "Field": "UnPost", "Count": ' + sqalFilter + ', "Type": "String", "FirstKey": "Status", "FirstValue": "POSTED","SecondValue" : "", "colorClass":"light-blue-bg" }';
    let SqlQuery = "select  '" + json + "' RecCount from APHDR where Poflg='" + psPoflg + "'   and ISNULL(UpdatedtoFin,0) in(0)"
    return SqlQuery;
  }

  public static bmgfGetPost(psPoflg) {
    let sqalFilter = "'+CAST(COUNT(vouchno) as nvarchar(10))+'";
    let json = '{ "Field": "Post", "Count": ' + sqalFilter + ', "Type": "String", "FirstKey": "Status", "FirstValue": "POSTED","SecondValue" : "", "colorClass":"light-blue-bg" }';
    let SqlQuery = "select  '" + json + "' RecCount from APHDR where Poflg='" + psPoflg + "'   and ISNULL(UpdatedtoFin,0) Not in(0)"
    return SqlQuery;
  }

