  public static bmgfGetUnPost() {
    let sqalFilter = "'+CAST(COUNT(PaymentNo) as nvarchar(20))+'";
    let json = '{ "Field": "UnPost", "Count": ' + sqalFilter + ', "Type": "String", "FirstKey": "Status", "FirstValue": "POSTED","SecondValue" : "", "colorClass":"light-blue-bg" }';
    let SqlQuery = "select  '" + json + "' RecCount from FinPaymentHdr where Status in ('NEW','HOLD')"
    return SqlQuery;
  }

  public static bmgfGetPost() {
    let sqalFilter = "'+CAST(COUNT(PaymentNo) as nvarchar(20))+'";
    let json = '{ "Field": "Post", "Count": ' + sqalFilter + ', "Type": "String", "FirstKey": "Status", "FirstValue": "POSTED","SecondValue" : "", "colorClass":"light-blue-bg" }';
    let SqlQuery = "select  '" + json + "' RecCount from FinPaymentHdr where Status in ('POSTED','VOID','REVERSE')"
    return SqlQuery;
  }
