  bmlfNgChanges(piRowId, psField) {
   
    this.mnumLineItemRowIndex = piRowId;
    this.mbChange = true;
    if (this.mArrGrid[piRowId].Flag.trim() != "A") {
      this.mArrGrid[piRowId].Flag = "M";
    }

    /*Abhay/22-Aug-2020/Bug# 141802*/
    if (psField == "Select") {
      if (this.mArrGrid[piRowId].Select == true) {
        this.mArrGrid[piRowId].Select = true;
      } else {
        this.mArrGrid[piRowId].Select = false;
      }
    } else {
      this.mArrGrid[piRowId].Select = true;
    }

    /*Abhay/10-July-2020/Bug# 141797*/
    if (psField == "New1099Amount") {
      if (this.msCallType == "Payment") {
        if (this.mArrGrid[piRowId].New1099Amount > this.mArrGrid[piRowId].PaymentTotal) {
          this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('Edit1099Information.Msg.Key4'));
          this.mArrGrid[piRowId].New1099Amount = this.mArrGrid[piRowId].Old1099Amount;
        }
      } else if (this.msCallType == "Voucher") {
        if (this.mArrGrid[piRowId].New1099Amount > this.mArrGrid[piRowId].VouchTotal) {
          this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('Edit1099Information.Msg.Key5'));
          this.mArrGrid[piRowId].New1099Amount = this.mArrGrid[piRowId].Old1099Amount;
        }
      }
    }
  }

