  public bmlfonProcess(isCallAfterSave = false): void {
   
    //check Write permission
    if (!this.appUtility.mbWrite) {
      this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    let pbResult = this.bmlfonValidateProcessData(); 
    if (pbResult == false) {
      return;
    }
    let psCustomDateFormat = CommonConstants.gsServerDateFormat; //this.msCustomDateFormat;
    this.mObjModelCriteria.FromDate =moment(this.mObjModelCriteria.FromDate).format(psCustomDateFormat.toUpperCase());
    this.mObjModelCriteria.ToDate =moment(this.mObjModelCriteria.ToDate).format(psCustomDateFormat.toUpperCase());

    let pSrequestObj: any = {
      HeaderData: [{ ...this.mObjModelCriteria }],
      ScreenOption: [{ "CallType": this.msCallType }],
      Response: [{ ResponseText: '', 'ErrorCode': '' }]
    };
    let reqData: any = {
      JSON: JSON.stringify(pSrequestObj)
    };
    this.objService.bmlfonGetData(reqData).subscribe(res => {
     
      this.mbProcessed = true;
      if (res == null) { return false; }
      if (!this.isEmpty(res.Response[0].ErrorCode)) {
        this.objToastService.notifyError(res.Response[0].ResponseText);
      } else {
        if (res.DetailData.length > 0) {
          let parrGridData = res.DetailData;
          parrGridData.forEach(ele => {
            if (this.msCallType == "Voucher") {
              ele.VoucherDate = new Date(ele.VoucherDate);
            } else if (this.msCallType == "Payment") {
              ele.PaymentDate = new Date(ele.PaymentDate);
            }
          });
          this.mArrGrid = res.DetailData;
          if (this.mArrGrid.length > 0) {
            if (!isCallAfterSave) {
              this.mbChange = true;
            }
          }
        }
      }
    });
  }

  // Add Line Item Grid 1
  public bmlfonAddLine(): void {
    if (!this.appUtility.mbWrite) {
      this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
    }
    if (this.mbProcessed) {
      if (this.msCallType == "Vendor") {
        this.bmlfOpenLookupModal(this.msScreenName, this.msLookupCaptionForVendor, this.msLookUpKeyForVendor, 'click', '', 'custom', 'AddLine', 0, false);
      }
      else if (this.msCallType == "Voucher") {
        this.bmlfOpenLookupModal(this.msScreenName, this.msLookupCaptionForVoucher, this.msLookUpKeyForVoucher, 'click', '', 'custom', 'AddLine', 0, false);
      }
      else if (this.msCallType == "Payment") {
        this.bmlfOpenLookupModal(this.msScreenName, this.msLookupCaptionForPayment, this.msLookUpKeyForPayment, 'click', '', 'custom', 'AddLine', 0, false);
      }
    } else {
      this.objToastService.notifyError((this.appUtility.bmgfonTranslate('Edit1099Information.Msg.Key3')));
      setTimeout(function () { document.getElementById('bmlfonProcess').focus(); }, 10);
      return;
    }
  }

  public bmlfonAddLineToGrid(parrLineData): void {
   
    let piLastIndex = 0;
    if (this.mArrGrid.length > 0) {
      piLastIndex = this.mArrGrid.length;
    }
    // parrLineData.forEach((ele) => {
    let pobjDataCopy: any = Object.assign({}, this.mObjDetailModel);
    pobjDataCopy.Flag = "A";
    pobjDataCopy.Select = true;
    if (this.msCallType == "Vendor") {
      pobjDataCopy.Vendor_Key = parrLineData.Vendor_Key;
      pobjDataCopy.Vendor_Name = parrLineData.Vendor_Name;
      pobjDataCopy.OldTypeKey = parrLineData.TypeKey1099;
      pobjDataCopy.OldTypeDesc = parrLineData.Type1099;
      pobjDataCopy.OldBoxNumber = parrLineData.Box1099;
      pobjDataCopy.OldBoxDescription = parrLineData.BoxDescription;
      pobjDataCopy.NewTypeKey = "";
      pobjDataCopy.NewTypeDesc = "";
      pobjDataCopy.NewBoxNumber = "";
      pobjDataCopy.NewBoxDescription = "";
    }
    else if (this.msCallType == "Voucher") {
      pobjDataCopy.VendKey = parrLineData.VendorKey;
      pobjDataCopy.VendName = parrLineData.VendorName;
      pobjDataCopy.VoucherNo = parrLineData.VoucherNo;
      //pobjDataCopy.VoucherDate = new Date(parrLineData.VoucherDate);
      pobjDataCopy.VoucherDate = this.appUtility.bmlfSetDateAsDateFormat(parrLineData.VoucherDate)
      pobjDataCopy.VouchTotal = parrLineData.VoucherTotal;
      pobjDataCopy.OldTypeKey = parrLineData.TypeKey1099;
      pobjDataCopy.OldTypeDesc = parrLineData.Type1099;
      pobjDataCopy.OldBoxNumber = parrLineData.Box1099;
      pobjDataCopy.OldBoxDescription = parrLineData.BoxDescription;
      pobjDataCopy.Old1099Amount = parrLineData.Amount1099;
      pobjDataCopy.NewTypeKey = "";
      pobjDataCopy.NewTypeDesc = "";
      pobjDataCopy.NewBoxNumber = "";
      pobjDataCopy.NewBoxDescription = "";
      /*Abhay/10-July-2020/Bug#141831 */
      pobjDataCopy.New1099Amount = parrLineData.Amount1099;
    }
    else if (this.msCallType == "Payment") {
      pobjDataCopy.Vendor = parrLineData.VendorKey;
      pobjDataCopy.Vendor_Name = parrLineData.VendorName;
      pobjDataCopy.PaymentNo = parrLineData.PaymentNo;
      //pobjDataCopy.PaymentDate = new Date(parrLineData.PaymentDate);
      pobjDataCopy.PaymentDate = this.appUtility.bmlfSetDateAsDateFormat(parrLineData.PaymentDate)
      pobjDataCopy.PaymentTotal = parrLineData.PaymentTotal;
      pobjDataCopy.OldTypeKey = parrLineData.TypeKey1099;
      pobjDataCopy.OldTypeDesc = parrLineData.Type1099;
      pobjDataCopy.OldBoxNumber = parrLineData.Box1099;
      pobjDataCopy.OldBoxDescription = parrLineData.BoxDescription;
      pobjDataCopy.Old1099Amount = parrLineData.Amount1099;
      pobjDataCopy.NewTypeKey = "";
      pobjDataCopy.NewTypeDesc = "";
      pobjDataCopy.NewBoxNumber = "";
      pobjDataCopy.NewBoxDescription = "";
      /*Abhay/10-July-2020/Bug#141831 */
      pobjDataCopy.New1099Amount = parrLineData.Amount1099;
    }
    pobjDataCopy.RecUserID = this.msUserId;
    pobjDataCopy.RecDate = new Date();
    this.mArrGrid.push(pobjDataCopy);
    // ++piLastIndex;
    // });
    let eleID = 'txtType1099' + (piLastIndex).toString();
    setTimeout(() => { document.getElementById(eleID).focus(); }, 10);
  }

