  /* Abhay/13-July-2020/Bug# 141471 */
  bmlfisValid(parrLineData): boolean {
   
    let pbflag = true;
    if (this.msCallType == "Vendor") {
      for (let index = 0; index < this.mArrGrid.length; index++) {
        if (this.mArrGrid[index]['Vendor_Key'] == parrLineData.Vendor_Key) {
          this.objToastService.notifyError((this.appUtility.bmgfonTranslate('Edit1099Information.Msg.Key8')));
          pbflag = false;
          break;
        }
      }
    } else if (this.msCallType == "Voucher") {
      for (let index = 0; index < this.mArrGrid.length; index++) {
        if (this.mArrGrid[index]['VendKey'] == parrLineData.VendorKey && this.mArrGrid[index]['VoucherNo'] == parrLineData.VoucherNo) {
          this.objToastService.notifyError((this.appUtility.bmgfonTranslate('Edit1099Information.Msg.Key9')));
          pbflag = false;
          break;
        }
      }
    } else if (this.msCallType == "Payment") {
      for (let index = 0; index < this.mArrGrid.length; index++) {
        if (this.mArrGrid[index]['Vendor'] == parrLineData.VendorKey && this.mArrGrid[index]['PaymentNo'] == parrLineData.PaymentNo) {
          this.objToastService.notifyError((this.appUtility.bmgfonTranslate('Edit1099Information.Msg.Key10')));
          pbflag = false;
          break;
        }
      }
    }
    return pbflag;
  }

