  //---------------------------------------------------------------------------------------------
  //copy
  bmlfonCopy(data) {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate(['/main/accountspayable/payment-entry/manage'], { queryParams: { PaymentNo: data.PaymentNo, CopyRecord: "Y" }, skipLocationChange: true });
  }
  //---------------------------------------------------------------------------------------------
  //Delete record
  bmlfonDelete(data) {
    // Only Status : New record can delete
    if (data.Status != 'NEW') {
      this.objToastService.notifyError(this.objUtil.bmgfonTranslate('PAYMENTENTRY.MSG.DELETESTATSCHECK'));
      return;
    }
    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };
    this.msPaymentKey = data.PaymentNo;
    this.mObjModel.PaymentNo = this.msPaymentKey;
    this.mbShowDeleteDialog = true;
  }
