    ;
    // this.mbSearchEvent = true;
    // let pbResult = this.bmlfonValidateFormData();
    // if (pbResult == false) {
    //   this.mbSearchEvent = false;
    //   if(this.mbInsert==false){
    //     return;
    //   }
    // }

    //if (this.mbChange == false) {
    if (!this.mbChange && !this.FieldExtender.mbChange && !this.customCtrl.mbChange) {
      this.bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx);
    }
    else {
      this.msCallFor = "Search";
      this.mbShowDialog = true;
    }
