  //---------------------------------------------------------------------------------------
  bmlfonValidateFormData() {

    if (this.mObjModel.PaymentNo.trim() == "") {
      if (this.mbSearchEvent == true) {
        return false;
      }
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.SELECTSERIES')));
      setTimeout(function () { document.getElementById('txtPaymentNo').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.PaymentType == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.PAYMENTYPEBLANK')));
      setTimeout(function () { document.getElementById('cmbPaymentType').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.PaymentType == "B" && this.mObjModel.PaymentMode.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.PAYMENTMODEBLANK')));
      setTimeout(function () { document.getElementById('txtPaymentMode').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.PaymentFromAc.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.PAYMENTACCTBLANK')));
      setTimeout(function () { document.getElementById('txtPaymentFromAc').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.Vendor == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.VENDCUSTACCTERR')));
      setTimeout(function () { document.getElementById('txtVendor').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.Currency_Key.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.CURRENCYKEYBLANK')));
      setTimeout(function () { document.getElementById('txtCurrencyKey').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.TotalAmount == 0) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.ZEROTOTALAMOUNT')));
      return false;
    }

    //--------------Grid Validation-----------------
    let errRow: string = '';
    let errFiled: string = '';
    let pirowindex: number = 0;
    let pirowindexRefType: number = 0;
    if (this.mArrPaymentEntryGrid.length > 0) {
      this.mArrPaymentEntryGrid.forEach(ele => {
        if (ele.errFiled != '') {
          if (ele.Amount == 0) {
            errRow += '' + errRow;
            errFiled = 'Amount';
          }
          if (ele.RefType == '') {
            errRow += '' + errRow;
            errFiled = 'RefType';
            pirowindexRefType = pirowindex;
          }
        }
        ++pirowindex;
      });

      if (errFiled == "RefType") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.ROWENTRYBLANK') + String(pirowindexRefType + 1)));
        return false;
      } else if (errFiled == "Amount") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.ZEROROWAMOUNT')));
        return false;
      }
    }
    //--------------Grid Validation-----------------

    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.objToastService.notifyWarning(resultString.msg);
      return false;
    }

    //Customization mandatory validation
    if (this.customGridCtrl != undefined) {
      this.customGridCtrl.bmlfGlobalIsMandatory()
    }


    return true;
  }
  //---------------------------------------------------------------------------------------
  bmlfonisEmpty(value) {
    return (typeof value === "undefined" || value === null || value === '');
  }
  //---------------------------------------------------------------------------------------
  bmlfonDeleteDialogAction(action) {

    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {
      if (this.msFormMode == "I" && (this.mbChange == true || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true)) {
        this.mbShowDeleteDialog = false;
        this.bmlfonClearControl();
        return;
      }
      else if (this.mbInsert && this.mbChange) {
        this.mbShowDeleteDialog = false;
        let deleteKey = "Payment Entry — " + this.mObjModel.PaymentNo;
        this.FieldExtender.bmgfDeleteData(deleteKey)
        this.bmlfonClearControl();
        this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('PRICEGROUP.MSGDELETE'));
        return false;
      }
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            PaymentNo: this.mObjModel.PaymentNo.trim()
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };

      this.PaymentEntryService.bmlfonDeletePaymentEntry(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError('Server Error');
          return false;
        }

        if (res.Response[0].ErrorCode == "") {

          if (res.Response[0].ResponseText.search("Reference") != -1) {
            this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.EXIST_REF'));
            this.mbShowDeleteDialog = false;
            return;
          }
          else if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
            this.mbShowDeleteDialog = false;
            this.objToastService.notifySuccess((this.objUtils.bmgfonTranslate('COMMON.MSG.DELETE')));
            this.bmlfonClearControl();
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText)
            this.mbShowDeleteDialog = false;
          }
        } else {
          this.objToastService.notifyError(res.Response[0].ResponseText)
          this.mbShowDeleteDialog = false;
        }

      })
    }
  }
