  //---------------------------------------------------------------------------------------
  //Note Open
  bmlfOpenNotesModal(psName: string, psValue: string) {
    if (psValue == " ") { psValue = "" };
    if (!CustomValidator.bmgfNullCheckValidator(psValue)) {
      if (psName == "Vendor") {
        if (this.mObjModel.VendMiscType == 0) {
          this.msNotesName = "VENDOR_KEY";
        } else if (this.mObjModel.VendMiscType == 1) {
          this.msNotesName = "CustomerKey";
        }
      }
      else if (psName == "Bank_ID") {
        if (this.mObjModel.PaymentType == "C") {
          this.msNotesName = "Cash_ID";
        } else if (this.mObjModel.PaymentType == "B") {
          this.msNotesName = "Bank_ID";
        } else if (this.mObjModel.PaymentType == "D") {
          this.msNotesName = "Card_ID";
        }
      }
      else {
        this.msNotesName = psName;
      }
      this.mbIsNotesModal = true;
      this.msNotesValue = psValue;
    } else {
      //Key cannot be empty
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.NOTES'));
    }
  }
