  //---------------------------------------------------------------------------------------
  bmlfonAddPaymentEntryLine() {

    //validation
    if (this.mObjModel.Vendor == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.VENDCUSTACCTERR')));
      return false;
    }
    let errRow: string = '';
    let errFiled: string = '';
    let pirowindex: number = 0;
    let pirowindexRefType: number = 0;
    if (this.mArrPaymentEntryGrid.length > 0) {
      this.mArrPaymentEntryGrid.forEach(ele => {
        if (ele.errFiled != '') {
          if (ele.Amount == 0) {
            errRow += '' + errRow;
            errFiled = 'Amount';
          }
          if (ele.RefType == '') {
            errRow += '' + errRow;
            errFiled = 'RefType';
            pirowindexRefType = pirowindex;
          }
        }
        ++pirowindex;
      });

      if (errFiled == "RefType") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.ROWENTRYBLANK') + String(pirowindexRefType + 1)));
        return false;
      } else if (errFiled == "Amount") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.ZEROROWAMOUNT')));
        return false;
      }
    }

    //--------------End VAlidation------------

    const pobjDataCopy: any = Object.assign({}, this.mObjModelGrid1);
    //----------Row No - S
    let piLastIndex;
    if (this.mArrPaymentEntryGrid.length > 0) {
      piLastIndex = this.mArrPaymentEntryGrid.reduce((max, obj) => obj.RowNo > max ? obj.RowNo : max, this.mArrPaymentEntryGrid[0].RowNo);
    } else {
      piLastIndex = this.mArrPaymentEntryGrid.length;
    }
    pobjDataCopy.RowNo = Number(piLastIndex) + 1;
    //----------Row No - E

    pobjDataCopy.Recuserid = this.msRecUserID;
    pobjDataCopy.Flag = "A";
    if (this.mObjModel.VendMiscType == 2) {//Miscellaneous
      pobjDataCopy.RefType = "New Ref";
      pobjDataCopy.isRefTypeDisabled = true;
      pobjDataCopy.isDocAmountDisabled = true;
      pobjDataCopy.isDiscountDisabled = false;
      pobjDataCopy.isWriteOffDisabled = false;
      pobjDataCopy.isBankChangreDisabled = true;
    }

    this.mArrPaymentEntryGrid.push(pobjDataCopy);

    // if(this.mObjModel.VendMiscType == 2){//Miscellaneous
    //   this.mArrPaymentEntryGrid1Col[1].Type = 'textReadOnly'; //Refrence Type Readonly
    // }

    this.mbVendorCustDisabled = true;
    this.mbCurrencyKeyDisabled = true;
    this.mbVendMiscTypeDisabled = true;
  }

  bmlfTDSShowColumn() {
    this.mArrPaymentEntryGrid1Col.forEach(col => {
      if (col.Field == "TDSAmt") {
        col.Display = false;
      }
    });
  }

