  //Print Check
  bmlfPaymentEntryPrintCheck() {
    if (this.mbPrintCheckToolButton == true) {
      return;
    }
    if (this.mObjModel.PaymentNo != '') {
      if ((this.mObjModel.PaymentMode.toUpperCase() != "CHECK" || this.mObjModel.PaymentMode == " ")) {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTENTRY.MSG.PAYMENTMODECHK')));
        return
      }

      let psCustomDateFormat: string = CommonConstants.gsServerDateFormat;
      let psVendorCust: string = "";
      if (this.mObjModel.VendMiscType == 0) {
        psVendorCust = "Vendor";
      } else {
        psVendorCust = "Customer";
      }

      let reqData: any;
      reqData = {
        JSON: JSON.stringify({
          DistReport: [{
            ReportName: "Print Check Report",
            DocNO: this.mObjModel.PaymentNo,
            CallFor: "NewPayment",
            VendorCustomer: psVendorCust,
            ListingFlag: false,
            ListingVariable: "",
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": "", "Pdfpath": ""
          }]
        })
      };

      this.objService.bmlfonDISTReportProcess(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError("Failure.");
        }
        if (res.Response[0].ErrorCode == "") {
          {
            // this.pdfSrc = res.Response[0].Pdfpath
            // this.modalservice.open('pdfViewerModal');
            let pDefaultReportType = localStorage.getItem('reportType');
            let pPDFSrc = res.Response[0].Pdfpath;
            if (pDefaultReportType == 'Print') {
              this.objUtils.bmgfPrintPDF(pPDFSrc);
            } else if (pDefaultReportType == 'NewTab') {
              this.objUtils.bmgfOpenPDFNewTab(pPDFSrc);
            } else {
              this.objUtils.bmgfDownloadFile(pPDFSrc, this.msscreenName, pDefaultReportType);
            }
          }
        } else {
          this.objToastService.notifyError(res.Response[0].ResponseText);
        }
      });
    }
  }

