  bmlfHandleControls(msCall) {

    //if(res.HeaderData[0].Status == 'POSTED' || res.HeaderData[0].Status == 'HOLD'){
    if (this.mObjModel.Status == 'POSTED') {
      this.mbDiabledAllFields = true;
      this.mbDisablePaymentMode = true;
      this.mbDisablePaymentFromAc = true;
      this.mbDisablePaymentDocNo = true;
      this.mbDisableChqBookID = true;
      this.mbDisablePaymentDocDate = true;
      this.mbVendorCustDisabled = true;
      this.mbCurrencyKeyDisabled = true;
      this.mbVendMiscTypeDisabled = true;
      this.mbCurrencyConversion = true;
      //Special Button
      this.mbPostToolButton = true;
      this.mbHoldToolButton = true;
      this.mbReleaseToolButton = true;
      this.mbVoidToolButton = false;
      this.mbReverseToolButton = false;
      // if(this.mObjModel.PaymentMode == 'Check'){
      this.mbPrintCheckToolButton = false;
      //  }
    } else if (this.mObjModel.Status == 'HOLD') {
      this.mbDiabledAllFields = true;
      this.mbDisablePaymentMode = true;
      this.mbDisablePaymentFromAc = true;
      this.mbDisablePaymentDocNo = true;
      this.mbDisableChqBookID = true;
      this.mbDisablePaymentDocDate = true;
      this.mbVendorCustDisabled = true;
      this.mbCurrencyKeyDisabled = true;
      this.mbVendMiscTypeDisabled = true;
      this.mbCurrencyConversion = true;

      //Special Button
      this.mbPostToolButton = true;
      this.mbHoldToolButton = true;
      this.mbReleaseToolButton = false;
      this.mbVoidToolButton = true;
      this.mbReverseToolButton = true;
      this.mbPrintCheckToolButton = true;
    } else if (this.mObjModel.Status == 'REVERSE' || this.mObjModel.Status == 'VOID') {
      this.mbDiabledAllFields = true;
      this.mbDisablePaymentMode = true;
      this.mbDisablePaymentFromAc = true;
      this.mbDisablePaymentDocNo = true;
      this.mbDisableChqBookID = true;
      this.mbDisablePaymentDocDate = true;
      this.mbVendorCustDisabled = true;
      this.mbCurrencyKeyDisabled = true;
      this.mbVendMiscTypeDisabled = true;
      this.mbDisablePaymentDocNo = true;
      this.mbCurrencyConversion = true;

      //Special Button
      this.mbPostToolButton = true;
      this.mbHoldToolButton = true;
      this.mbReleaseToolButton = true;
      this.mbVoidToolButton = true;
      this.mbReverseToolButton = true;
      this.mbPrintCheckToolButton = true;
    }
    else if (this.mObjModel.Status == 'NEW' && this.mObjModel.PaymentNo == '') {
      this.mbDiabledAllFields = false;
      this.mbDisablePaymentMode = false;
      this.mbDisablePaymentFromAc = false;
      this.mbDisablePaymentDocNo = false;
      this.mbDisableChqBookID = true;
      if (this.mObjModel.PaymentMode.toUpperCase() == 'CHECK') {
        this.mbDisableChqBookID = false;
      }
      this.mbDisablePaymentDocDate = false;
      this.mbVendorCustDisabled = false;
      this.mbCurrencyKeyDisabled = false;
      this.mbVendMiscTypeDisabled = false;
      this.mbCurrencyConversion = false;
      //Special Button
      this.mbPostToolButton = true;
      this.mbHoldToolButton = true;
      this.mbReleaseToolButton = true;
      this.mbVoidToolButton = true;
      this.mbReverseToolButton = true;
      this.mbPrintCheckToolButton = true;
    } else if (this.mObjModel.Status == 'NEW' && this.mObjModel.PaymentNo != '') {
      this.mbDiabledAllFields = false;
      this.mbDisablePaymentMode = false;
      this.mbDisablePaymentFromAc = false;
      this.mbDisablePaymentDocNo = false;
      this.mbDisableChqBookID = true;
      if (this.mObjModel.PaymentMode.toUpperCase() == 'CHECK') {
        this.mbDisableChqBookID = false;
      }
      this.mbDisablePaymentDocDate = false;
      this.mbVendorCustDisabled = true;
      this.mbCurrencyKeyDisabled = true;
      this.mbVendMiscTypeDisabled = true;
      this.mbCurrencyConversion = false;
      //Special Button
      this.mbPostToolButton = false;
      this.mbHoldToolButton = false;
      this.mbReleaseToolButton = true;
      this.mbVoidToolButton = true;
      this.mbReverseToolButton = true;
      this.mbPrintCheckToolButton = false;
      if (this.mArrPaymentEntryGrid.length == 0) { //Avanish 24Aug20
        this.mbVendorCustDisabled = false;
        this.mbCurrencyKeyDisabled = false;
        this.mbVendMiscTypeDisabled = false;
      }
    }

  }
