  bmlfReveralService() {
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          PaymentNo: this.mObjModel.PaymentNo,
          ReverseDate: moment(this.mdtReversalDate).format(CommonConstants.gsServerDateFormat)//this.mdtReversalDate
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfonReversePaymentEntry(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res.Response[0].ErrorCode == "") {
        //this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate('COMMON.MSG.SAVE'));
        this.mbChange = false;
        this.msMsgData.screenName = this.msscreenName;
        this.msMsgData.Data = res.Response[0].ResponseText;
        this.mbBulkResponseView = true;
        this.msReport = res.Response[0].ResponseText;
        this.mbReportDialog = true;
        this.bmlfGetDataOnLoad(this.mObjModel.PaymentNo, 'Both');
      } else {
        this.objToastService.notifyError(res.Response[0].ResponseText);
      }
    });
  }
