  bmlfonChangeConvFactor() { //Avanish 01Sep20
    this.mbChange = true;
    let piIndex: number = 0;
    this.mArrPaymentEntryGrid.forEach(ele => {
      ele.AmountBankCurr = ele.Amount * this.mObjModel.CurrConvFact;
      ele.DocAmtBankCurr = ele.DocAmount * this.mObjModel.CurrConvFact;
      ele.DiscountBankCurr = ele.Discount * this.mObjModel.CurrConvFact;
      ele.BankChargesBankCurr = ele.BankCharges * this.mObjModel.CurrConvFact;
      ele.WriteOffAmtBankCurr = ele.WriteOffAmt * this.mObjModel.CurrConvFact;
    })
  }

