export class ProcessApplyPaymentModel {
    public OptionPayment: boolean;
    public OptionDebitNote: boolean;
    public PaymentNo: string;
    public CurrencyKey: string;
    public BatchSeries: string;
    public ApplyDate: Date;
    public PaymentDate: Date;
    public PaymentLineno: number;
    public RemainingAmount: number;
    public TotalAppliedAmount: number;
    public TotalWriteOffAmount: number;
    public VendorCustomer: number;
    public Vend_CustKey: string;
    constructor() {
        this.OptionPayment = true;
        this.OptionDebitNote = false;
        this.PaymentNo = '';
        this.CurrencyKey = '';
        this.BatchSeries = '';
        this.ApplyDate = new Date();
        this.PaymentDate = new Date();
        this.PaymentLineno = 0;
        this.RemainingAmount = 0;
        this.TotalAppliedAmount = 0;
        this.TotalWriteOffAmount = 0;
        this.VendorCustomer = 0;
        this.Vend_CustKey = '';
    }
}
export class ProcessApplyPaymentDetailsModel {
    public Sel: boolean;
    public VoucherNo: string;
    public DueDate: Date;
    public Amount: number;
    public RemAmount: number;
    public ApplyAmount: number;
    public WriteOff: number;
    public JrnlBtchNo: string;
    public RecordPtr: number;
    public DocType: string;
    constructor() {
        this.Sel = false;
        this.VoucherNo = '';
        this.DueDate = new Date();
        this.Amount = 0;
        this.RemAmount = 0;
        this.ApplyAmount = 0;
        this.WriteOff = 0;
        this.JrnlBtchNo = '';
        this.RecordPtr = 0;
        this.DocType = '';
    }
}
export class UnappliedAmount {
    public Vend_CustKey: string;
    public CurrencyKey: string;
    public Type: string;
    constructor() {
        this.Vend_CustKey = '';
        this.CurrencyKey = '';
        this.Type = 'Vendor';
    }
}

export class PaymentDetails {
    public PaymentNo: string;
    constructor() {
        this.PaymentNo = '';
    }
}

export class UnadjustedAmount {
    public PaymentNo: string;
    public PaymentLineno: number;
    public OptionPayment: boolean;
    public VendorCustomer: number;
    constructor() {
        this.PaymentNo = '';
        this.PaymentLineno = 0;
        this.OptionPayment = false;
        this.VendorCustomer = 0;
    }
}

export class VoucherDebitDetails {
    public RefType: string;
    public Vend_CustKey: string;
    public CurrencyKey: string;
    public DocNo: string;
    public PaymentDate: Date;
    public VendorCustomer: number;
    constructor() {
        this.RefType = '';
        this.Vend_CustKey = '';
        this.CurrencyKey = '';
        this.DocNo = '';
        this.PaymentDate = new Date();
        this.VendorCustomer = 0;
    }
}
