import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class TdsPaymentProcessingService {

  constructor(private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService) {
  }

  public bmlfonGetTDSPayment(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/GetTDSPayment`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfonPostTDSPayment(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/PostTDSPayment`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonGetFinanceSeries(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/GetFinanceSeries`;
    return this.httpExt.post<any>(url, reqdata, null);
  }


}
