  bmlfGetCurrenyAll() {
      let pobjReqData = {
        ScreenName: 'All Screen',
        LookUpKey: 'CurrencyConversion',
        Filter1: new Date(),
      }
      this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
          if (res.LookupData.length > 0) {
            this.mscurrenyFacture  = res.LookupData;
             this.msHomeCurreny= this.mscurrenyFacture[0].HomeCurrency;


          }
      });
    }

    bmlfCalCurrenyFacture(checkbookCurreny) {
      this.mArrPaymentRangeGrid_B.forEach(ele => {
         //var facture = this.msGetCurrenyCOnvertFacture(checkbookCurreny ,ele.CurrencyKey )
         var facture = this.msGetCurrenyCOnvertFacture(ele.CurrencyKey, checkbookCurreny)
            ele.CurrConvFact = facture
            ele.CurrConvDate = new Date();
            ele.RemAmtBankCurr = ele.RemAmount * facture
            ele.PaidAmountBankCurr = ele.PaidAmount * facture
            ele.DiscountBankCurr = ele.Discount * facture
            ele.BankChargesBankCurr = ele.BankCharges * facture
            ele.WriteOffBankCurr = ele.WriteOffAmt * facture
      });
    }




    msGetCurrenyCOnvertFacture(fromcurreny ,ToCurrency  ){
      var fromcurenybyRate =1;
      var ToCurrencybyRate =1;
      if(fromcurreny == ToCurrency){
          return 1;
      }else {
        this.mscurrenyFacture.forEach(ele => {
            if(ele.CurrencyKey ==fromcurreny){
                fromcurenybyRate = ele.BuyRate;
            }else if(ele.CurrencyKey ==ToCurrency){
                ToCurrencybyRate = ele.BuyRate;
            }
          });
           if(fromcurreny ==  this.msHomeCurreny ){
                return 1/ToCurrencybyRate;
           }else if(ToCurrency ==this.msHomeCurreny ){
                return fromcurenybyRate;
           }else{
                return ToCurrencybyRate/fromcurenybyRate;
           }
      }
  }





