  bmlfRedirectFunctionality() {
    if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.User1)) {
      let queryParamsObj = { queryParams: {} };
      queryParamsObj.queryParams[this.mbHyerLinkData.data.User2] = this.mbHyerLinkData.msField1Value;
      if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.KeyValue2)) {
        queryParamsObj.queryParams[this.mbHyerLinkData.data.User3] = this.mbHyerLinkData.msField2Value;
      }
      this.router.navigate([this.mbHyerLinkData.data.User1], queryParamsObj);
    }
  }
  //---------------------------------------------------------------------------------------
  bmlfGetDataOnLoad(msDocNo) {
    

    this.mArrPaymentRangeGrid_A = [];
    this.mArrPaymentRangeGrid_B = [];
    this.marrAllVoucherList = [];
    this.maarraySelected = [];
    this.msVendorKey = "";
    this.msVendorName = "";
    this.mnVendTotalAmtPaid = 0;
    this.mnVendTotalAmtPaidBC = 0;

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          DocNo: msDocNo
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfonGetPaymentRangeProcess(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res.HeaderData.length > 0) {
        this.msFormMode = "U";
        this.mbDisabledPrimaryKey = true;
        this.mObjModel = res.HeaderData[0];
        this.mbCustomControl = true;
        this.msBankId = String(this.mObjModel.BankID);
        this.mObjModel.ApplyDate = new Date(res.HeaderData[0].ApplyDate);
        
        if(this.mObjModel.PaymentMode == 'Check'){
          this.mbIsPrintIcon=true;
        }
        if (this.mObjModel.ChqPerVendor == '0') {
          this.mbOneCheckPerVendorChecked = true;
          this.mbOneCheckPerVoucherChecked = false;
        } else {
          this.mbOneCheckPerVendorChecked = false;
          this.mbOneCheckPerVoucherChecked = true;
        }

        //Vendor Data
        if (res.VendorData.length > 0) {
          let parrVendorGridData = res.VendorData;
          parrVendorGridData.forEach(ele => {
            //ele.DueDate = new Date(ele.DueDate);
          })
          this.mArrPaymentRangeGrid_A = parrVendorGridData; //res.VendorData;
        }
        //Voucher Date
        if (res.VoucherData.length > 0) {
          this.mObjModel.TotalAmount = 0;
          let parrVoucherGridData = res.VoucherData;
          parrVoucherGridData.forEach(ele => {
            ele.DueDate = moment(ele.DueDate).format(this.msCustomDateFormat.toUpperCase());
            ele.CurrConvDate = new Date(ele.CurrConvDate); //moment(ele.CurrConvDate).format(this.msCustomDateFormat.toUpperCase());
            this.mObjModel.TotalAmount += ele.PaidAmountHome;
          })
          this.mArrPaymentRangeGrid_B = parrVoucherGridData; //res.VoucherData;
          this.marrAllVoucherList = parrVoucherGridData;//SCR141438
          this.bmlfHandleControls();//Disable Enable on
          this.bmlfGetcheckBookDetail();
          this.bmlfCheckEsignApplicableOnForm();
          this.bmlfCalCurrenyFacture( this.mObjModel.BankCurrency)
          this.bmlfSetCurrencyFactor(); // Avanish 6Apr22 Case:2183-1020

         // this.bmlfSetCurrencyFactor(); // Avanish 6Apr22 Case:2183-1020
        }
      } else {
        this.msFormMode = "I";
      }

    });
  }


  bmlfSetCurrencyFactor() {  
    this.marrAllVoucherList.forEach(ele =>{ 
        var facture = this.msGetCurrenyCOnvertFacture(ele.CurrencyKey ,this.msHomeCurrency )
        ele.HomeCurFactor = facture
      });   
  }





