  //---------------------------------------------------------------------------------------
  bmlfonAdd() {
    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    if (this.msCallFor == "Copy") {
      this.mbChange = true;
    }
    this.msCallFor = "Add";
    let pbResult = this.bmlfonValidateFormData();
    if (pbResult == true) {
      if (this.mObjModel.DocNo == "") {
        this.mbShowDialog = true;
      } else {
        this.mObjModelSelectionCriteria = Object.assign({}, this.mObjModelSelectionCriteriaBlank);
        this.msToVendorClassDesc = '';
        this.msFromVendorClassDesc = '';
        this.msFromVendorKeyDesc = '';
        this.msToVendorKeyDesc = '';
        this.msFromVoucherDate = '';
        this.msToVoucherDate = '';
        this.msBankName = '';
        this.msAccountNo = '';
        this.msBankId = '';
        this.msDocNo = '';
        this.mbSelectionDialog = true;
      }
    }
  }
