  //---------------------------------------------------------------------------------------
  calculateTotalAmount() {
    //this.mnTotalAmount=0;
    this.mObjModel.TotalAmount = 0;
    this.mnVendTotalAmtPaid = 0;
    this.mnVendTotalAmtPaidBC = 0;
    
    
    /*
     this.maarraySelected.forEach(ele => {
         this.mnTotalAmount += ele.PaidAmount;
         //Currency Convrsion
     });
*/
        

    this.mArrPaymentRangeGrid_B.forEach(ele => {
      if (ele.Sel == true) {
        ele.PaidAmountHome = (ele.PaidAmount * ele.HomeCurFactor); // Avanish 6Apr22 Case:2183-1020

      }
    })

    this.marrAllVoucherList.forEach(ele => {
      if (ele.Sel == true) {
        ele.PaidAmountHome = (ele.PaidAmount * ele.HomeCurFactor)
        this.mObjModel.TotalAmount += ele.PaidAmountHome;
      }
      //Currency Convrsion
    });

    this.mnVendTotalAmtPaid = 0;
    this.mArrPaymentRangeGrid_A.forEach(ele => {
      ele.TotalAmountPaid = 0;
      ele.TotalPaidAmountBankCurr = 0;
      let parrFilterdArr: any = [];
      parrFilterdArr = this.mArrPaymentRangeGrid_B.filter(x => x.VendorKey == ele.VendorKey);

      let mbTemp = false;
      parrFilterdArr.forEach(obj => {

        if (ele.VendorKey == this.msVendorKey) {
          this.mnVendTotalAmtPaid += obj.PaidAmountHome;
          this.mnVendTotalAmtPaidBC += obj.PaidAmountBankCurr;
        }
        if (obj.Sel == true) {
          if (mbTemp == false) {
            ele.TotalAmountPaid = 0;
            ele.TotalPaidAmountBankCurr = 0;
            ele.TotalAmountPaid = obj.PaidAmountHome;
            ele.TotalPaidAmountBankCurr = obj.PaidAmountBankCurr;
            mbTemp = true;
          } else {
            ele.TotalAmountPaid += obj.PaidAmountHome;
            ele.TotalPaidAmountBankCurr += obj.PaidAmountBankCurr;
          }
        }

      });
    })

  }
  //---------------------------------------------------------------------------------------
  bmlfonselectionChange(e) {
    
    if (e.selectedRows.length > 0) {
      e.selectedRows.forEach(ele => {
        ele.dataItem.PaidAmount = ele.dataItem.DocAmount;
        ele.dataItem.Sel = true;
        this.maarraySelected.push(ele.dataItem);
      })
      this.calculateTotalAmount();
    }
    else if (e.deselectedRows.length > 0) {
      e.deselectedRows.forEach(ele => {
        ele.dataItem.Sel = false;
        ele.dataItem.PaidAmount = 0;
        ele.dataItem.PaidAmountHome = 0;
        ele.dataItem.PaidAmountBankCurr = 0;
        //ele.dataItem.PaidAmountBankCurr = 0;
        ele.dataItem.Discount = 0;
        ele.dataItem.DiscountBankCurr = 0;
        ele.dataItem.BankCharges = 0;
        ele.dataItem.BankChargesBankCurr = 0;
        ele.dataItem.WriteOffAmt = 0;
        ele.dataItem.WriteOffBankCurr = 0;

        let pobjTempData = ele.dataItem;
        this.maarraySelected = this.maarraySelected.filter(x => {
          if (x.VendDocNo == pobjTempData.VendDocNo) {
            return x;
          }
        });
      });
      this.calculateTotalAmount();
    }

  }
