  bmlfonSelectRow(rowNum) {
    this.mbChange = true;
    let pVendorKey = '';
    let pPaidamount = 0;
    if (this.mArrPaymentRangeGrid_B[rowNum].Sel == true ) {
      this.mArrPaymentRangeGrid_B[rowNum].PaidAmount = this.mArrPaymentRangeGrid_B[rowNum].RemAmount;
      this.mArrPaymentRangeGrid_B[rowNum].PaidAmountBankCurr = this.mArrPaymentRangeGrid_B[rowNum].PaidAmount * (this.mArrPaymentRangeGrid_B[rowNum].CurrConvFact == 0 ? 1 : this.mArrPaymentRangeGrid_B[rowNum].CurrConvFact);
      if(this.mArrPaymentRangeGrid_B[rowNum].CurrencyKey !=  this.msHomeCurrency){           
            var facture = this.msGetCurrenyCOnvertFacture(this.mArrPaymentRangeGrid_B[rowNum].CurrencyKey ,this.msHomeCurrency )
            this.mArrPaymentRangeGrid_B[rowNum].HomeCurFactor =facture 
            this.mArrPaymentRangeGrid_B[rowNum].PaidAmountHome = (this.mArrPaymentRangeGrid_B[rowNum].PaidAmount * (facture));
      }else{
        this.mArrPaymentRangeGrid_B[rowNum].HomeCurFactor = 1
        this.mArrPaymentRangeGrid_B[rowNum].PaidAmountHome = (this.mArrPaymentRangeGrid_B[rowNum].PaidAmount);     
      }
      this.calculateTotalAmount();
    } else {
      this.mArrPaymentRangeGrid_B[rowNum].PaidAmount = 0;
      this.mArrPaymentRangeGrid_B[rowNum].PaidAmountHome = 0;
      this.mArrPaymentRangeGrid_B[rowNum].PaidAmountBankCurr = 0;
      this.mArrPaymentRangeGrid_B[rowNum].Discount = 0;
      this.mArrPaymentRangeGrid_B[rowNum].DiscountBankCurr = 0;
      this.mArrPaymentRangeGrid_B[rowNum].BankCharges = 0;
      this.mArrPaymentRangeGrid_B[rowNum].BankChargesBankCurr = 0;
      this.mArrPaymentRangeGrid_B[rowNum].WriteOffAmt = 0;
      this.mArrPaymentRangeGrid_B[rowNum].WriteOffBankCurr = 0;
      this.mArrPaymentRangeGrid_B[rowNum].ChqBookID = '';
      this.mArrPaymentRangeGrid_B[rowNum].BankID = 0;
      this.mArrPaymentRangeGrid_B[rowNum].ChequeNo = 0;
      this.calculateTotalAmount();
    }

  }





