   //select deselect all checkbox
   bmlfSelectAllRow(value) {
    this.mbChange = true;
    this.mArrPaymentRangeGrid_B.forEach(ele=>{
      if (value == true){
        ele.Sel = true;
        ele.PaidAmount = ele.RemAmount;


        ele.PaidAmount = ele.RemAmount;
        ele.PaidAmountBankCurr = ele.PaidAmount * (ele.CurrConvFact == 0 ? 1 :ele.CurrConvFact);
          if(ele.CurrencyKey !=  this.msHomeCurrency){           
                var facture = this.msGetCurrenyCOnvertFacture(ele.CurrencyKey ,this.msHomeCurrency )
                ele.HomeCurFactor =facture 
                ele.PaidAmountHome = (ele.PaidAmount * (facture));
          }else{
            ele.HomeCurFactor = 1
            ele.PaidAmountHome = (ele.PaidAmount);     
          }
          this.calculateTotalAmount();
      

      }
      else{
        ele.Sel = false;
        ele.PaidAmount = 0;
        ele.PaidAmountHome = 0;
        ele.PaidAmountBankCurr = 0;
        ele.Discount = 0;
        ele.DiscountBankCurr = 0;
        ele.BankCharges = 0;
        ele.BankChargesBankCurr = 0;
        ele.WriteOffAmt = 0;
        ele.WriteOffBankCurr = 0;
        ele.ChqBookID = '';
        ele.BankID = 0;
        ele.ChequeNo = 0;
        this.calculateTotalAmount();
      }
      
    })
}
