  //---------------------------------------------------------------------------------------
  bmlfonAssighCheckNumber() {
    
    this.mbChange = true;

    if (this.mObjModel.ChqBookID == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('PAYMENTRANGEPROCESSING.MSG.CHECKBOOKBLANK')));
      setTimeout(function () { document.getElementById('txtChqBookID').focus(); }, 10);
      return false;
    }


    let tempLastcheckNum = this.mnLastcheckNum;

    if (this.mObjModel.ChqPerVendor == '1') {
      //one Check Per voucher
      let parrFilterdArr: any = [];
      parrFilterdArr = this.marrAllVoucherList.filter(x => x.Sel == true);
      parrFilterdArr.forEach(ele => {
        ele.ChequeNo = tempLastcheckNum + 1;
        ele.BankID = this.msBankId;
        ele.ChqBookID = this.mObjModel.ChqBookID;
        tempLastcheckNum = tempLastcheckNum + 1;
      });
    } else {
      //one Check Per Voucher
      this.mArrPaymentRangeGrid_A.forEach(obj => {
        let parrFilterdArr: any = [];
        parrFilterdArr = this.marrAllVoucherList.filter(x => x.Sel == true && x.VendorKey == obj.VendorKey);
        if (parrFilterdArr.length > 0) {
          tempLastcheckNum = tempLastcheckNum + 1;
          parrFilterdArr.forEach(ele => {
            ele.ChequeNo = tempLastcheckNum;
            ele.BankID = this.msBankId;
            ele.ChqBookID = this.mObjModel.ChqBookID;
          });
        }
      });
    }
  }
