  bmlfGeneratePaymentDocument() {
    //Generate Payment Document
    
    if (this.mObjModel.DocNo != '' && this.mObjModel.TemplateID != '') {

      let reqData: any;
      reqData = {
        JSON: JSON.stringify({
          HeaderData: [{
            DocNO: this.mObjModel.DocNo,
            TemplateKey: this.mObjModel.TemplateID,
            StartRow: "0",
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };

      this.objService.bmlfGetPaymentRangeExcelTemplate(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError("Failure.");
        }
        if (res.Response[0].ErrorCode == "") {
          {
            if (res.ExcelData.length > 0) {
              this.exportToExcel(res.ExcelData);
            }
          }
        } else {
          this.objToastService.notifyError(res.Response[0].ResponseText);
        }
      });
    }

  }
  //---------------------------------------------------------------------------------------
  bmlfPrintCheckDoc() {
    // Print Check Document
    
    if (this.mObjModel.PaymentMode != 'Check') {
      this.mbPrintCheckDocToolButton = false;
    }

    if (this.mbPrintCheckDocToolButton == false) {
      return;
    }
    if (this.mObjModel.DocNo != '') {

      let reqData: any;
      reqData = {
        JSON: JSON.stringify({
          DistReport: [{
            ReportName: "Print Check Document",
            DocNO: this.mObjModel.DocNo,
            CallFor: "NewCheck",
            VendorCustomer: "Vendor",
            ListingFlag: false,
            ListingVariable: "",
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": "", "Pdfpath": ""
          }]
        })
      };

      this.objService.bmlfonDistReportProcess(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError("Failure.");
        }
        if (res.Response[0].ErrorCode == "") {
          {
            // this.pdfSrc = res.Response[0].Pdfpath
            // this.modalservice.open('pdfViewerModal');
            let pDefaultReportType = localStorage.getItem('reportType');
            let pPDFSrc = res.Response[0].Pdfpath;
            if (pDefaultReportType == 'Print') {
              this.objUtils.bmgfPrintPDF(pPDFSrc);
            } else if (pDefaultReportType == 'NewTab') {
              this.objUtils.bmgfOpenPDFNewTab(pPDFSrc);
            } else {
              this.objUtils.bmgfDownloadFile(pPDFSrc, this.msscreenName, pDefaultReportType);
            }
          }
        } else {
          this.objToastService.notifyError(res.Response[0].ResponseText);
        }
      });
    }
  }

