  public bmlfGetPaymentModeID(): void {
    //+++++++++++++ Payment Mode  ++++++++++++++++++++++++++
    let psPaymentMode: string = this.mObjModel.PaymentMode;
    if (psPaymentMode == "") {
      psPaymentMode = "Check";
    }
    let pobjReqData = {
      ScreenName: 'PaymentRangeProcessComponent',
      LookUpKey: 'PaymentModeID',
      Filter1: psPaymentMode
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mnumPaymentModeID = res.LookupData[0].PaymentModeID;
        if (this.mnumPaymentModeID != 0 && this.mObjModel.PaymentMode == "") {
          this.mObjModel.PaymentMode = psPaymentMode;
        }
      }
    });
  }

  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }

  bmlfGetDefaultSeries() {
    let pobjReqData = {
      ScreenName: 'PaymentRangeProcessComponent',
      LookUpKey: 'PaymentEntrySeries',
      Filter1: this.msRecUserID
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mObjModel.BatchSeries = res.LookupData[0].DefaultSeries;
        this.mbChange = false;
      }
    });
  }

  // check e signature
  bmlfCheckEsignApplicableOnForm() {
    let pobjReqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          MenuScreenName: this.msscreenName,
        }],
        Response: [{
          ResponseText: '',
          ErrorCode: ''
        }]
      })
    };
    this.userService.bmlfCheckEsignApplicable(pobjReqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.Response[0].ErrorCode == '') {
        this.mbIsEsignature = res.ESignData[0].Permision;
        this.mbIsEsignADTPermision = res.ESignData[0].ADTPermision;
        this.mobjSignatureData.reasonMinLength = res.ESignData[0].ReasonLength;
        this.mobjSignatureData.reqSignAuth = res.ESignData[0].NoOfReqSign;
      }
    }, err => {
      console.log(err);
    });
  }

  // open e signature modal
  bmlfopenESignatureModal() {
    this.mbSignatureModal = true;
    this.mobjSignatureData.isShow = true;
    this.mobjSignatureData.primaryKey = this.mObjModel.DocNo;
  }

  // check e signature
  bmlfChkESignature(pobjData) {
    this.mbSignatureModal = pobjData.isShow;
    if (pobjData.event == 'Save') {
      this.mbIsEsignature = false;
      this.mObjModel.ESG_REASON = pobjData.changeReason;
      this.mObjModel.ESG_APPROVER = pobjData.eSignApprover;
      this.mbChange = true;
      this.bmlfonSubmitData();
    } else if (pobjData.event == 'Cancel') {
      this.mobjCommonAlertData = {}
      this.mbCommonAlertDialog = true;
      this.mobjCommonAlertData.message = this.objUtils.bmgfonTranslate('ESIGNREASONMSG');
      this.mobjCommonAlertData.event = 'close';
      this.mbChange = true;
    }
  }

  // ESIGN Audit alert event
  bmlfEsignAuditAlertEvent(event) {
    if (event == 'No') {

    } else {
      this.mbIsEsignAuditDialog = false;
    }
  }
  // CommonAlertDialog
  bmlfmbCommonAlertDialog(event) {
    this.mbCommonAlertDialog = false;
  }

  // begin tcs 04/mar/2021 deepak
  public mbTCSApplicable: boolean = false;
  getTCSApplicable() {
    
    let parrDefaultAppSetup = JSON.parse(localStorage.getItem("DistributionParameter"));
    if (parrDefaultAppSetup) {
      parrDefaultAppSetup.forEach((ele) => {
        if (ele.DEFAULT_KEY == 'TCS Applicable') {
          if (ele.DEFAULT_VALUE == 'YES' || ele.DEFAULT_VALUE == 'Yes') {
            this.mbTCSApplicable = true;
            this.bmlfTCSShowColumn();
          }

        }
      });
    }
  }
  // end tcs 04/mar/2021 deepak
  bmlfTCSShowColumn() {
    if (this.mbTCSApplicable == true) {
      this.mArrPaymentRangeGrid_BCol.forEach(col => {
        if (col.Field == "User8") {
          col.Display = true;
        }
        if (col.Field == "User9") {
          col.Display = true;
        }
      });
    }
    else {
      this.mArrPaymentRangeGrid_BCol.forEach(col => {
        if (col.Field == "User8") {
          col.Display = false;
        }
        if (col.Field == "User9") {
          col.Display = false;
        }
      });
    }

  }

  public bmlfValidateTcsPaidAmount(piRowId) {
    
    if (this.mArrPaymentRangeGrid_B[piRowId].User9 > this.mArrPaymentRangeGrid_B[piRowId].User8) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('PAYMENTRANGEPROCESSING.MSG.Key14'));
      this.mArrPaymentRangeGrid_B[piRowId].User9 = 0;
    }
  }

  // close report modal
  bmlfOnRptAction(event) {
    this.mbDescriptionDialog = false;
    this.bmlfGetDataOnLoad(this.mObjModel.DocNo);
  }


  bmlfOnProcess() {
    if(!this.mbIsPrintIcon){
      return;
    }
    this.mbIsLoading=true;
    if (this.mObjModel.DocNo != '') {
      let reqData: any;
      reqData = {
        JSON: JSON.stringify({
          DistReport: [{
            ReportName: "Print Check Report",
            DocNo: this.mObjModel.DocNo,
            VendorCustomer: "",
            CallFor:this.mObjModel.Status == 'NEW' ? "NewCheck" : "Check",
            ListingFlag: false,
            ListingVariable: "",
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": "", "Pdfpath": ""
          }]
        })
      };

      this.objService.bmlfonDistReportProcess(reqData).subscribe(res => {
        this.mbIsLoading=false;
        if (res == null) {
          this.objToastService.notifyError("Failure.");
        }
        if (res.Response[0].ErrorCode == "") {
          {
            // this.pdfSrc = res.Response[0].Pdfpath
            // this.modalservice.open('pdfViewerModal');
            let pDefaultReportType = localStorage.getItem('reportType');
            let pPDFSrc = res.Response[0].Pdfpath;
            if (pDefaultReportType == 'Print') {
              this.objUtils.bmgfPrintPDF(pPDFSrc);
            } else if (pDefaultReportType == 'NewTab') {
              this.objUtils.bmgfOpenPDFNewTab(pPDFSrc);
            } else {
              this.objUtils.bmgfDownloadFile(pPDFSrc, this.msscreenName, pDefaultReportType);
            }
            this.bmlfGetDataOnLoad(this.mObjModel.DocNo);
          }
        } else {
          this.mbIsLoading=false;
          this.objToastService.notifyError(res.Response[0].ResponseText);
        }
      });
    }
  }
