import { Component, OnInit, Renderer2, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { LookupService } from 'src/app/shared/http/lookup.service';
import * as moment from 'moment';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { OnezeroEnquiryService } from '../../http/onezero-enquiry.service';
import { EnquirytModel } from '../../models/1099-enquiry.model';
import { enquiryConstants } from '../../constant/1099-enquiry.const';
import { CustomValidator } from 'src/app/core/services/custom.validators';
@Component({
  selector: 'app-onezero-enquiry',
  templateUrl: './onezero-enquiry.component.html',
  styleUrls: ['./onezero-enquiry.component.scss']
})
export class OnezeroEnquiryComponent implements OnInit {



    public msDecFrom: string;  
    public msDecTo: string; 
    public msDecYear: string;

    public mbIsLoading: boolean = false;
    public msPrdKey: string;
    public msScreenName: string = '1099 Enquiry';
    public mbIsChangeData: boolean = false;
    public msCustomDateFormat: string = "";
    public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
    public autoCorrect: boolean = true;
    public msFormMode: string = "I";
    /**---Object Initialize---- */
    public mobjEnquiryModel: EnquirytModel = new EnquirytModel();
    public marrItemCol: any = enquiryConstants.MonthItemCol.map(ele => Object.assign({}, ele));
    public marrItemList: any = enquiryConstants.marrItemList.map(ele => Object.assign({}, ele));
    public mobjUserData: any = {};
    public mnumDeleteLineIndex: number;
    public mbDeleteLineItemDialog: boolean = false;
    public mbShowDeleteDialog: boolean = false;
    /**---Lookup realted field--- */
    public mbIsLookupModal: boolean = false;
    public mobjLookupKeyData: any = {};
    public marrLookupGridData: any = [];
    public msModalTitle: string = ''
    public mnumLineItemRowIndex: number = -1;
    public marrArrayList: any = [];
    /**---END : Lookup realted field--- */

    /**-------START: TASK Field ----*/
    public mbIsTaskModal: boolean = false;
    public mobjTaskData: any = {};
    /**-------END: TASK Field ----*/

    public mbCustomControl = false;
    public mskeyValue: string = ""; //FieldExtender
    // 170138
    public msGridcol = [
        { Type: 'Text', Display: true, Field: 'Vendor_Key', Title: 'VENDORKEY', Index:0 },
        { Type: 'Text', Display: true, Field: 'Vendor_Name', Title: 'VENDORNAME', Index:1 },
        { Type: 'Text', Display: true, Field: 'Month', Title: 'MONTH', Index:2 },
        { Type: 'Text', Display: true, Field: 'Type1099', Title: 'TYPE', Index:3 },
        { Type: 'Text', Display: true, Field: 'Box1099', Title: 'BOX', Index:4 },
        { Type: 'Text', Display: true, Field: 'Amount1099', Title: 'AMOUNT', Index:5 },
    ]

    constructor(private route: ActivatedRoute,
        private router: Router,
        private formService: OnezeroEnquiryService,
        private lookupService: LookupService,
        private toastMessageService: ToastMessageService,
        private dateFormatService: DateFormatService,
        public utilService: AppUtility,
        public userFieldService: UserFieldService, private lsService: LocalStorageService) {
    }
    ngOnInit() {
        /*--------- get screen permission --------*/
        this.utilService.getBMEScreePermission(this.msScreenName);
        /**--- Get default date format from service--- */
        this.msCustomDateFormat = this.dateFormatService.getDateFormat();
        this.mobjUserData = this.lsService.getUserSession();

        this.mobjEnquiryModel.MonthFrom=1
        this.mobjEnquiryModel.MonthTo=1

    }



    bmlfCheckValidation(){
        if (CustomValidator.bmgfNullCheckValidator(this.mobjEnquiryModel.Year)) {
            this.bmlfOpenErrorBlock("ENQUIRY1099.SELECTYEAR");
            return false;
        }else if (CustomValidator.bmgfNullCheckValidator(this.mobjEnquiryModel.MonthFrom)) {
            this.bmlfOpenErrorBlock("ENQUIRY1099.FROMMONTHREQ");
            return false;
        } else if (CustomValidator.bmgfNullCheckValidator(this.mobjEnquiryModel.MonthTo)) {
            this.bmlfOpenErrorBlock("ENQUIRY1099.TOMONTHREQ");
            return false;
        } else {
            return true;
        }
    }

    bmlfGet1099EnquiryData() {
        if(!this.bmlfCheckValidation()){
            return;
        }
        this.mbIsLoading = true;
        this.marrArrayList =[];
        let pobjOData = { HeaderData:[] , Response: [] };
        pobjOData.HeaderData = [this.mobjEnquiryModel]
        pobjOData.Response.push({   "ResponseText": "", "ErrorCode": "" })
        let psJData = JSON.stringify(pobjOData);
        let psTempjData = JSON.stringify(psJData);
        let psJson = '{"JSON":' + psTempjData + '}';
        this.formService.bmlfGet1099Enquiry(psJson).subscribe( res => {
                this.mbIsLoading = false;
                if (res.Response[0].ErrorCode == "") {
                    if(res.Response[0].ResponseText != 'Success'){
                        this.bmlfOpenErrorBlock(res.Response[0].ResponseText);
                    }
                   
                    this.marrArrayList= res.Enquiry;
                }else{
                    this.marrArrayList= res.Enquiry;
                }
            }
        ).add(() => {
            this.mbIsLoading = false;
        })
    }
    bmlfOnFormFieldChange() {
        this.mbIsChangeData = true;
        if(this.mobjEnquiryModel.VendorKeyFrom ==''){
            this.msDecFrom ='';
        }
        if(this.mobjEnquiryModel.VendorKeyTo == ''){
            this.msDecTo ='';
        }
        if(this.mobjEnquiryModel.Year ==''){
            this.msDecYear ='';
        }


        let psValue = this.mobjEnquiryModel.VendorKeyFrom.toUpperCase();
        let psCtrlValue = this.mobjEnquiryModel.VendorKeyTo.toUpperCase();
        if ('VendorKeyFrom'.includes("From") == true) {
          if (this.mobjEnquiryModel.VendorKeyTo == "") { 
            return true
          } else if (psValue > psCtrlValue) {//if (value > this.mobjOrderModel[ControlMap[ctrlName]]) {
            this.mobjEnquiryModel.VendorKeyTo = ""
            this.bmlfOpenErrorBlock('ENQUIRY1099.T0VALUE');
            return false
          } else {
            return true
          }
        } else {
          if (this.mobjEnquiryModel.VendorKeyFrom == "") {
            return true
          }
          else if (psValue < psCtrlValue) {//(value < this.mobjOrderModel[ControlMap[ctrlName]]) {
            this.mobjEnquiryModel.VendorKeyFrom = ""
            this.bmlfOpenErrorBlock('ENQUIRY1099.FROMVALUE');
            return false
          }
          else {
            return true
          }
        }



    }



    bmlfOnMonthFieldChange(pstype){
       
       
        var MonthFrom = this.mobjEnquiryModel.MonthFrom;
        var MonthTo = this.mobjEnquiryModel.MonthTo;
        if (pstype == 'MonthFrom') {
          if (this.mobjEnquiryModel.MonthTo == "") { 
            return true
          } else  {//if (value > this.mobjOrderModel[ControlMap[ctrlName]]) {
              console.log( MonthFrom  < MonthTo )

            if ((Number( MonthFrom)  > Number(MonthTo) ) == true){
                this.mobjEnquiryModel.MonthFrom = Number( 0)
               // this.mobjEnquiryModel.MonthTo = 1
                this.bmlfOpenErrorBlock('ENQUIRY1099.T0VALUEM');
                return false
            }else{
                return true
            }
           
          }
        } else {
          if (this.mobjEnquiryModel.MonthFrom == "") {
            return true
          }
          else {

            console.log( MonthTo  < MonthFrom )


            if (Number(MonthTo) < Number(MonthFrom)  ) {//(value < this.mobjOrderModel[ControlMap[ctrlName]]) {
               // t//his.mobjEnquiryModel.MonthFrom = ''
               this.mobjEnquiryModel.MonthTo = Number( 0 )
                this.bmlfOpenErrorBlock('ENQUIRY1099.FROMVALUEM');
                return false
              }else{
                return true
              }
          }
        }

    }






    bmlfOpenErrorBlock(psMsg): void {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
    }
    /**--------Start: Lookup Functionality----- */
    // LOOKUP MODAL 
    bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {
        this.mbIsLoading = true;
        this.marrLookupGridData = [];
        this.mobjLookupKeyData = {};
        this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
        if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
            this.mobjEnquiryModel[psField] = "";
            this.mbIsLoading = false;
            return false
        }
        let psReqData = {
            ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: "", Filter2: "", Filter3: "", Filter4: '', Filter5: ''
        };

        this.lookupService.getLookup(psReqData).subscribe(res => {
            this.mbIsLoading = false;
            if (res == null) {
                this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
                return false;
            }
            if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
                res.LookupData.forEach((ele) => {
                    Object.keys(ele).forEach((key) => {
                        let psKey = key.toUpperCase();
                        if (psKey.search('DATE') != -1) {
                            if (ele[key] && ele[key] != '') {
                                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
                            } else {
                                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
                            }
                        }
                    });
                })
                this.marrLookupGridData = res.LookupData;
                this.mnumLineItemRowIndex = pnumRowIdx;
                this.mobjLookupKeyData.field = psField;
                this.mobjLookupKeyData.gridData = this.marrLookupGridData;
                this.mobjLookupKeyData.type = psType;
                this.mobjLookupKeyData.eleId = psEleId;

                if (psValue) {
                    this.mobjLookupKeyData.searchValue = psValue.toString();
                } else {
                    this.mobjLookupKeyData.searchValue = '';
                }
                if (psEvent == 'blur' && psValue) {
                    let parrFilterdArr: any = [];
                    /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
                    let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
                    parrFilterdArr = this.marrLookupGridData.filter(obj => {
                        if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
                            return obj;
                        }
                    });
                    if (parrFilterdArr.length == 1) {
                        this.mobjLookupKeyData.value = parrFilterdArr[0];
                        this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
                        this.bmlfSaveLookupField(this.mobjLookupKeyData);
                        this.mbIsLookupModal = false;
                    } else {
                        this.mbIsLookupModal = true;
                    }
                } else if (psEvent == 'click') {
                    this.mbIsLookupModal = true;
                }

            } else {
                this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
            }
        }, err => {
            console.log(err);
            this.mbIsLoading = false;
        })
    }

    /** save lookup field data */
    bmlfSaveLookupField(pobjEvent) {
        this.mbIsLoading = false;
        this.mbIsLookupModal = false;
        if (pobjEvent.type == 'custom') {
            this.mobjEnquiryModel[pobjEvent.field] = pobjEvent.valueKey;
            if(pobjEvent.field =='VendorKeyFrom'){
               this.msDecFrom = pobjEvent.value.Vendor_Name;
            }else if(pobjEvent.field =='VendorKeyTo'){
               this.msDecTo =pobjEvent.value.Vendor_Name;
            }else{
                this.msDecYear =pobjEvent.value.Vendor_Name;
            }
            this.bmlfOnFormFieldChange();
        }
    }

     /*-- get Lookup Value Column -- */ // 
     bmlfGetInputColumn(psField) {
        if (psField == 'VendorKeyFrom' || psField == 'VendorKeyTo' ) {
            return 'Vendor_Key';
        }else{
            return 'FiscalYear';
        }
    }

     /*-- Close LookUp Modals -- */
     bmlfCloseLookupModal(pobjData) {
        this.mbIsLookupModal = pobjData.isLookupModal;
        if (pobjData.eleId != null) {
            let psInputKey = this.bmlfGetInputColumn(pobjData.field);
            let parrFilterData: any = [];
            if (psInputKey) {
                parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
            }
            if (parrFilterData.length == 0) {
                this.mobjEnquiryModel[pobjData.field] = '';
                this.mbIsChangeData = true;
            }
            document.getElementById(pobjData.eleId).focus();
        }
    }
    /**--------End: Lookup Functionality----- */
    bmlfCloseEnquiry(): void {
       this.router.navigate(["/main/dashboard/dashboardpage"]);
    }

    //Open Task MODAL 
    bmlfOpenTaskModal(taskType) {
        this.mobjTaskData = {};
        this.mobjTaskData.taskType = taskType;
        if (taskType == 'myTask') {
            this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
            this.mobjTaskData.innerRecord = this.mobjUserData;
        } else if (taskType == 'recordTask') {
            this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
            let pobjFormRec: any = {};
            pobjFormRec.formName = this.msScreenName;
           // pobjFormRec.primeKey = this.mobjEnquiryModel.Productkey;
            this.mobjTaskData.innerRecord = pobjFormRec;
        }
        this.mbIsTaskModal = true;
    }

    //Close Task MODAL 
    bmlfonCloseTaskModal(pobjData) {
        this.mbIsTaskModal = pobjData.isTaskModal;
    }

    bmlfViewHelpDoc() {
        window.open('../../../../assets/help files/1099 Enquiry.pdf', '_blank');
    }

    bmlfonEdit(data,psType) {
        if(psType =='Vendor_Key'){
            this.router.navigate(['/main/purchase/vendor/manage'], {
                queryParams: {
                    VendKey: data.Vendor_Key
                }
            });
        }else if(psType =='Type1099'){
            if(this.marrArrayList !=''){
                this.router.navigate(['/main/financeconfiguration/1099-type-setup'])
            }
        }else{
            if(this.marrArrayList  !=''){
                this.router.navigate(['/main/financeconfiguration/1099-type-setup'])
            }
        }
    }
}