import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class ApplyPaymentService {
  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
  }

  public bmlfonGetPaymentDetails(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetPaymentDetails`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonGetUnAppliedAmount(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/GetUnappliedAmount`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfonGetUnAdjustedAmount(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetUnadjustedAmount`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonGetVoucherDebitDetails(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/FetchVoucherDebitDetails`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonProcessApplyPayment(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/ProcessApplyPayment`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonGetFinanceSeries(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetFinanceSeries`;
    return this.httpExt.post(url, reqdata, null);
  }

  public GetUnadjustedTCSAmount(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetUnadjustedTCSAmount`;
    return this.httpExt.post(url, reqdata, null);
  }
}
