@Component({
  selector: 'app-report1099',
  templateUrl: './report1099.component.html',
  styleUrls: ['./report1099.component.scss']
})
export class Report1099Component implements OnInit {

  public msselectedType:any='';
  //public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public mbIsLoading: boolean = false;
  public msCustomDateFormat: string = "";
  public mbLoaderEnabled: boolean = false;
  public mobjFormData: Report1099Modal = new Report1099Modal();
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public mbEditMode = false;
  public msFormMode = 'I';
  public msCopyMode = 'N';
  public mbIsSaveDone: boolean = false;
  public mbIsChange: boolean = false;
  public mbIsLookupFocus: boolean = false;
  public msLookupField: any = '';
  public msScreenName: string = '1099 Report';
  public mbMutiSelectionLookup = true;
  public msCommonMsg: any = '';
  public paramValueArray = [];
  public parameterValue: any = '';
  public screenModeUpdate: string;
  public checkval = false;
  public mobjUserData: any = {};
  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ----*/ 
  public msSelectedDateType: any = '';
  
  
  private paramKey = {
    VendorKeyFrom: 'txtVendorKeyFrom',
    VendorKeyTo: 'txtVendorKeyTo',
    "1099Type": 'txt1099Type',
    ForFiscalYear: 'txtForFiscalYear',
    UsePrePrintedStationery: 'chkUsePrePrintedStationery',
    CopyA: 'radioCopyA',
    Copy1: 'radioCopy1',
    CopyB: 'radioCopyB',
    Copy2: 'radioCopy2',
    CopyC: 'radioCopyC',
    VendorKeyFromDesc:'lbl-from-desc',
    VendorKeyToDesc:'lbl-to-desc',
    "1099TypeDesc":'lbl-type-desc'
  }


  @ViewChild('dataListCtrl') dataListCtrl: ElementRef;
  msSelectedPostType: any;
  mbisUsePreprinted: boolean=false;


  constructor(
    private lookupService: LookupService,
    private modalservice: ModalService,
    public dateFormatService: DateFormatService,
    private lsService: LocalStorageService,
    private router: Router,
    public utilService: AppUtility,
    public toastMessageService: ToastMessageService,
    public userFieldService: UserFieldService,
    private alertservice: AlertService,
    private formService: CommonReportService
    //  private service : InventoryReportService,
  ) {

  }

  /**
   * On Init Component
   */
  ngOnInit() {
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    // uncomment later on when link is generated
    // this.utilService.getBMEScreePermission(this.msScreenName);
    this.mobjUserData = this.lsService.getUserSession();
    this.msCommonMsg = this.utilService.bmgfonTranslate('TENNINTYNINEREPORT.MESSAGE.TOFROMVALUECHECK');
    
    this.getParam(this.msScreenName);
    // -- End
    //  this.bmlfGetDetails();

    //  this.bmlfCheckEsignApplicableOnForm();
  }

  // Close Oreder Modal
  bmlfCloseForm(type) {
    this.router.navigate(['/main/dashboard/']);
  }


  /**
   * On Order Action
   */
  bmlfOnSaveDetails() {
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('ATTACHSOTOPALLET.MESSAGE.PERMISSION_WRITE'));
      return false;
    }

    let isMendentFileds = this.bmlfValidateData();
    if (isMendentFileds) {
      return false;
    }


    if (this.mbIsSaveDone) {
      return false;
    }

    if (!this.mbIsChange && this.mbEditMode) {
      return false;
    }
    this.mbIsChange = false;

    // this.bmlfonAssignCustValues(custom);
    this.onProcess('pdf');

  }

  onProcess(pdf) {
    let isMendentFileds = this.bmlfValidateData();
    if (isMendentFileds) {
      return false;
    }

    this.mbIsLoading = true;
    //this.mObjModel.ReportName ="Inventory Analysis Report";
    let pDefaultReportType = localStorage.getItem('reportType');
    let reportObj = {};
    let reportData: any = {
      JSON: JSON.stringify({
        DistReport: [{
          "ReportName": this.msScreenName,
           "VendorKeyFrom":this.mobjFormData.VendorKeyFrom,
           "VendorKeyTo":this.mobjFormData.VendorKeyTo,
           "1099Type":this.mobjFormData['1099Type'],
           "ForFiscalYear":this.mobjFormData.ForFiscalYear,
           "CopyA":this.mobjFormData.CopyA == true ? true : false,
           "Copy1":this.mobjFormData.Copy1 == true ? true : false,
           "CopyB":this.mobjFormData.CopyB == true ? true : false,
           "Copy2":this.mobjFormData.Copy2 == true ? true : false,
           "CopyC":this.mobjFormData.CopyC == true ? true : false,
           "UsePrePrintedStationery":this.mbisUsePreprinted,
           "ListingFlag": false,
           "ListingVariable": ''
        }],
        Response: [{ 'ResponseText': '', 'ErrorCode': '', 'Pdfpath': '' }]
      })
    };


    this.formService.GetReportData(reportData).subscribe(res => {

      this.mbIsLoading = false;
      if (res != null) {
        //if (res.Response[0].ResponseText == "Success") {
        if (res.Response[0].ErrorCode == "") {
          //this.pdfSrc = res.Response[0].Pdfpath
          //this.modalservice.open(id);
          let pPDFSrc = res.Response[0].Pdfpath;
          if (pDefaultReportType == 'Print') {
            this.utilService.bmgfPrintPDF(pPDFSrc);
          } else if (pDefaultReportType == 'NewTab') {
            this.utilService.bmgfOpenPDFNewTab(pPDFSrc);
          } else {
            
            this.utilService.bmgfDownloadFile(pPDFSrc, this.msScreenName,pDefaultReportType);
          }

        }
        else {
          if (res.Response[0].ErrorCode != '') {

            this.onOpenErrorBlock(res.Response[0].ResponseText) //scr 152829 
          }
          else {
            this.onOpenErrorBlock("No Data Found")
          }
          this.mbIsLoading = false;
        }
      }
    }, error => {
      this.mbIsLoading = false;
    });
  }
  //Open LOOKUP MODAL
  bmlfonOpenLookupModal(sType, psField, psEvent, psValue, psType, psEleId, piRowIdx = null) {
    if (psEvent == 'blur' && this.msLookupField == psValue) {
      this.msLookupField = '';
      this.mobjFormData[psField + 'Desc'] = '';
      return false;
    }

    this.mbMutiSelectionLookup = false;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};

    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjFormData[psField] = '';
      this.mobjFormData[psField + 'Desc'] = '';
      return;
    }

    if (psEvent == 'blur' && (psValue == undefined || psValue == '' || this.msLookupField == psValue)) {
      this.mbIsLoading = false;
      this.msLookupField = '';
      if (psValue == undefined || psValue == '') {
        this.mobjFormData[psField + 'Desc'] = '';
      }
    }

    let psLookUpKey = this.bmlfonSetLookupKey(psField, sType);
    let psLookupTitle = this.bmlfSetLookupTitle(psField);
    let psScreenName = this.msScreenName;
    let pobjReqData: any = {}

   
    
      pobjReqData = { ScreenName: psScreenName, LookUpKey: psLookUpKey }
    





    // this.utilService.bmgfOnKeyInputDisable();
    this.mbIsLoading = true;
    this.lookupService.getLookup(pobjReqData).subscribe(res => {
      setTimeout(() => {
        this.utilService.bmgfOnKeyInputEnable();
        this.mbIsLoading = false;
      }, 500);

      if (res == null) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.NO_DATA_MSG'));
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {

        this.marrLookupGridData = res.LookupData;
        this.mobjLookupKeyData.field = psField;
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
              }
            }
          });
        })


        //this.mobjLookupKeyData.title = title + ' Lookup';
        this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psLookupTitle);
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue.toString();
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let psInputKey = this.bmlfGetInputColumn(psField);
          let parrFilterData: any = [];
          parrFilterData = this.marrLookupGridData.filter(obj => {
            let tempValue = obj[psInputKey] == null || obj[psInputKey] == '' ? '' : (obj[psInputKey].toString()).toUpperCase();
            if (psInputKey == 'Lang') {
              if (tempValue.trim() == (psValue.toString()).toUpperCase()) {
                return obj;
              }
            } else {
              if (tempValue.trim() == (psValue.toString()).toUpperCase()) {
                return obj;
              }
            }
          });

          if (parrFilterData.length == 1) {
            this.mobjLookupKeyData.value = parrFilterData[0]
            this.bmlfonSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else if (parrFilterData.length == 0) {

            this.mobjFormData[psField] = '';
            let elementExists = document.getElementById(psEleId);
            if (elementExists) {


              document.getElementById(psEleId).focus();
            }
            this.mbIsLookupModal = true;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        this.mobjFormData[psField] = '';
        if (psEleId) {

          document.getElementById(psEleId).focus();
        }
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
      }
    }, err => {
      console.log(err);
    });
  }
  // Global focus Fn
  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }

  //Close LOOKUP MODAL
  bmlfonCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    let psInputDesKey = this.bmlfGetInputDesColumn(pobjData.field);
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => (obj[psInputKey].toString()).toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0) {
        this.mobjFormData[pobjData.field] = '';

      }
      document.getElementById(pobjData.eleId).focus();
    }
  }

  bmlfonSaveLookupField(pobjData) {
    
    let str = pobjData.field.slice(-4);
    let insertName: any;
    if(this.trimCheck(str)){
      insertName = pobjData.field.slice(0,pobjData.field.length - 4);
        this.mobjFormData[insertName + 'To'] = '';
        this.mobjFormData[insertName + 'ToDesc'] = '';
    }
    else{
      
      insertName = pobjData.field.slice(0,pobjData.field.length - 2);
      if(pobjData.valueKey.length > 0){
        if(this.mobjFormData[insertName + 'From'] && this.mobjFormData[insertName + 'From'] > pobjData.valueKey){
          this.alertservice.infoAlert('Batchmaster', this.msCommonMsg);
          this.mobjFormData[pobjData.field]='';
          this.mobjFormData[insertName + 'ToDesc'] = '';
          this.mbIsLookupModal = false;
          return;
        }
      }
      
    }
    let psInputKey = this.bmlfGetInputColumn(pobjData.field);
    let psInputDesKey = this.bmlfGetInputDesColumn(pobjData.field);
    this.mbIsLookupModal = false;
    this.mbMutiSelectionLookup = false;
    if (!Array.isArray(pobjData.value)) {
      if (pobjData.type == 'custom') {
        this.mobjFormData[pobjData.field] = pobjData.value[psInputKey];
        this.mobjFormData[pobjData.field + 'Desc'] = pobjData.value[psInputDesKey]

        this.bmlfonGlobalOnChangesFn(pobjData.field, pobjData, null);
      }
    } else {
      //   this.bmlfonTransferValues(pobjData.field, pobjData.value, undefined, undefined);
    }

  }
  trimCheck(str) {
    if (str == 'From') { return true; }
    else { return false; }
  }


  //  Set Lookup Description Column
  bmlfGetInputDesColumn(psField) {
    
    if (psField == 'VendorKeyFrom' || psField == 'VendorKeyTo') {
      return 'VendorName'
    } 
    if(psField == '1099Type'){
      return 'Type1099'
    }
  }

  // Set Lookup KEY
  bmlfonSetLookupKey(psField, printType) {
    let psLookUpKey;
    if (psField == 'VendorKeyFrom') {
      return psLookUpKey = 'VendorKeyFrom';
    } else if (psField == 'VendorKeyTo'){
      return psLookUpKey = 'VendorKeyTo'
    }
    else if (psField == '1099Type') {
      return psLookUpKey = '1099Type';
    } else if (psField == 'ForFiscalYear') {
      return psLookUpKey = 'ForFiscalYear';
    }
    return psLookUpKey

  }
  bmlfSetLookupTitle(psField) {
    let psLookupTitle;
    if ((psField == 'VendorKeyFrom' || psField == 'VendorKeyTo')) {
      psLookupTitle = 'TENNINTYNINEREPORT.VENDORLOOKUP'
    }  else if ((psField == '1099Type')) {
      psLookupTitle = 'TENNINTYNINEREPORT.TYPELOOKUP'
    } else if ((psField == 'ForFiscalYear')) {
      psLookupTitle = 'TENNINTYNINEREPORT.FISCALYEARLOKUP'
    }

    return psLookupTitle;
  }


  // get Lookup Value Column
  bmlfGetInputColumn(psField) {
    if ((psField == 'VendorKeyFrom' || psField == 'VendorKeyTo')) {
      return 'Vendor_Key';
    } else if ((psField == '1099Type')) {
      return 'TypeKey1099';
    } else if ((psField == 'ForFiscalYear')) {
      return 'FiscalYear';
    } 

  }


  // Open Modal
  bmlfonOpenModal(psId, piIndex = null) {
    this.modalservice.open(psId);
  }

  bmlfonGlobalOnChangesFn(psFieldName, pobjFieldData, piIndex = null) {
    if (pobjFieldData == null) {
      return false;
    }
    let str = psFieldName.slice(-4);
    let insertName: any;
    if(this.trimCheck(str)){
      insertName = psFieldName.slice(0,psFieldName.length - 4);
        this.mobjFormData[insertName + 'To'] = '';
        this.mobjFormData[insertName + 'ToDesc'] = '';
    }
    else{
      insertName = psFieldName.slice(0,psFieldName.length - 2);
      if(pobjFieldData.length > 0){
        if(this.mobjFormData[insertName + 'From'] && this.mobjFormData[insertName + 'From'] > pobjFieldData){
          this.alertservice.infoAlert('Batchmaster', this.msCommonMsg);
          this.mobjFormData[psFieldName]='';
          this.mobjFormData[insertName + 'ToDesc'] = '';
          this.mbIsLookupModal = false;
          return;
        }
      }
     
    }
    this.mbIsChange = true;
    this.mbIsSaveDone = false;
  }

  // Global Blur Fn
  bmlfOnGlobalBlur(field, value) {
    if (field == 'lookup') {
      this.mbIsLookupFocus = false;
    }
  }


  // Refresh Form
  bmlfRefreshForm() {
    this.utilService.bmgfRefreshReuse('/main/accountspayable/1099-report', false);
  }



  bmlfValidateData(evenType?) {
    
    let isMendentFiled = false;
    if(CustomValidator.bmgfNullCheckValidator(this.mobjFormData['1099Type'])){
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('TENNINTYNINEREPORT.MESSAGE.TENNINNTYNINETYPEBLANK'));
    //  this.onOpenErrorBlock(this.utilService.bmgfonTranslate('TENNINTYNINEREPORT.MESSAGE.TENNINNTYNINETYPEBLANK'))
      isMendentFiled=true;
    }
    if(CustomValidator.bmgfNullCheckValidator(this.mobjFormData.ForFiscalYear)){
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('TENNINTYNINEREPORT.MESSAGE.FISCALYEARBLANK'));
    //  this.onOpenErrorBlock(this.utilService.bmgfonTranslate('TENNINTYNINEREPORT.MESSAGE.TENNINNTYNINETYPEBLANK'))
      isMendentFiled=true;
    }
    return isMendentFiled;
  }
  onParamchange(e) {
    try {
      let pValue = e.target.value;
      let optionArray = [...this.dataListCtrl.nativeElement.options];
      optionArray.some(res => {
        if (pValue == res.value) {
          this.fetchParamData(pValue)
          return true;
        }
      })
    }
    catch (e) {
      console.log(e);
      console.trace();
    }
  }
  private fetchParamData(param) {
    let pValue = param;
    this.mbIsLoading = true;
    this.formService.getReportParameterValue(this.msScreenName, param).subscribe(res => {
      if (res != null && res.RPTPARAMETERSBYVALUE.length > 0) {
        this.clearModel();
        this.fillControlFromParam(res.RPTPARAMETERSBYVALUE);
      }
      else {
        console.error('No Response');
        console.trace();
        this.mbIsChange = false;
      }
    })
  }
  private fillControlFromParam(resData) {
    let selectedData: any;

    resData.forEach(element => {
      selectedData = this.paramKey;
      let key = this.getKeyFromValue(element.CONTROLNAME, selectedData);
      if (element.CONTROLNAME == 'Option1' && element.CONTROLVALUE == 'True') {
        this.mbisUsePreprinted = true;
      } else{
        this.mbisUsePreprinted = false;
      }
      if (element.CONTROLNAME == 'radioCopyA' && element.CONTROLVALUE == 'True') {
        this.mobjFormData.copyOption = 'CopyA';
        this.mobjFormData.CopyA=true;
      }
      if (element.CONTROLNAME == 'radioCopyB' && element.CONTROLVALUE == 'True') {
        this.mobjFormData.copyOption = 'CopyB';
        this.mobjFormData.CopyB=true;
      }
      if (element.CONTROLNAME == 'radioCopy1' && element.CONTROLVALUE == 'True') {
        this.mobjFormData.copyOption = 'Copy1';
        this.mobjFormData.Copy1=true;
      }
      if (element.CONTROLNAME == 'radioCopy2' && element.CONTROLVALUE == 'True') {
        this.mobjFormData.copyOption = 'Copy2';
        this.mobjFormData.Copy2=true;
      }
      if (element.CONTROLNAME == 'radioCopyC' && element.CONTROLVALUE == 'True') {
        this.mobjFormData.copyOption = 'CopyC';
        this.mobjFormData.CopyC=true;
      }
      if(element.CONTROLNAME == 'lbl-to-desc'){
        this.mobjFormData.VendorKeyToDesc=element.CONTROLVALUE
      } 
      if(element.CONTROLNAME == 'lbl-from-desc'){
        this.mobjFormData.VendorKeyFromDesc=element.CONTROLVALUE
      }
     if (key == 'DateFrom' || key == 'DateTo') {
        this.mobjFormData[key] = new Date(element.CONTROLVALUE)
      }
      else if(element.CONTROLNAME == 'txtVendorKeyTo' || element.CONTROLNAME == 'txtVendorKeyFrom' || element.CONTROLNAME == 'txt1099Type' || element.CONTROLNAME == 'txtForFiscalYear'){
        this.mobjFormData[key] = element.CONTROLVALUE;
      }
      
    })
    //scr 152828

    this.mbIsLoading = false;
  }
  private getKeyFromValue(value, selectedData) {
    let tempKey = Object.keys(this.mobjFormData).find(data => {
      if (data != 'orderType') {
        return selectedData[data] == value
      }
    });
    return tempKey;
  }
  private clearModel() {
    Object.keys(this.mobjFormData).forEach(keys => {
      this.mobjFormData[keys] = " ";
    })

  }
  private getParam(scr) {
    this.mbIsLoading = true;
    this.formService.getReportParameter(scr).subscribe(res => {
      if (res != null) {
        this.paramValueArray = res.RPTPARAMETERS;
        this.mbIsLoading = false;

        //scr 152839

      }
      else {
        console.trace();
        console.error("Res Is Null");
        this.mbIsLoading = false;
      }
    })
  }
  onParameterSave() {
    
    //let paramValue = this.paramCtrl.nativeElement.value;
    let paramValue = this.parameterValue;
    let newObj = this.mobjFormData;
    let paramKey;
    paramKey = this.paramKey;
    //delete newObj['Desc_Lot_No'];
    if (paramValue == '') {
      this.onOpenErrorBlock('Parameter cannot be empty');
      return;
    }
    else {
      this.mbIsLoading = true;
      this.screenModeUpdate = "I";
      let jObj = {};
      let headerData = [];
      let jArray = [];
      let counterRecId = 0;
      jObj["ParamName"] = paramValue;
      jObj["ScreenName"] = this.msScreenName;
     

      if (this.paramValueArray.length > 0) {
        let list = this.paramValueArray.filter(x => x.PARANAME === paramValue)[0];
        if (list != undefined) {
          this.screenModeUpdate = 'U';
          jObj["UpdateMode"] = this.screenModeUpdate;
        } else {
          jObj["UpdateMode"] = this.screenModeUpdate;
        }
      } else {
        jObj["UpdateMode"] = this.screenModeUpdate;
      }

      headerData.push(jObj);
      Object.keys(newObj).map(key => {
          console.log("keys", key)
            let tempJObj = {};
               tempJObj["ControlName"] = paramKey[key]
               tempJObj["ControlValue"] = newObj[key]
            
            tempJObj["RecId"] = counterRecId++;
            jArray.push(tempJObj);
          
        });
        if (this.mbisUsePreprinted == true) {
          jArray.push({
            'ControlName': 'Option1',
            'ControlValue': 'True',
            "RecId": counterRecId++
          });
        } else  if (this.mbisUsePreprinted == false) {
          jArray.push({
            'ControlName': 'Option1',
            'ControlValue': 'False',
            "RecId": counterRecId++
          });
        }
      

      this.formService.saveReportParameters(jArray, headerData).subscribe(res => {
        if (res != null) {
          if (res['Response'][0].ResponseText == "Sucess") {
            //this.paramCtrl.nativeElement.value = "";
            this.onOpenErrorBlock('Save Successfully.');
            this.parameterValue = '';
            this.mbIsLoading = false;
            this.getParam(this.msScreenName);
          }
          else {
            alert('Cant Save');
            console.trace();
            console.error('Save error');
            this.mbIsLoading = false;
          }
        }
      })
    }

  }
  onParameterDelete() {

    //let oParaValue = this.paramCtrl.nativeElement.value;
    let oParaValue = this.parameterValue;
    if (oParaValue == "") {
      this.onOpenErrorBlock('Select Report Parameter.');
      return;
    }
    else {
      let oArray = [...this.dataListCtrl.nativeElement.options];
      if (oArray.length != 0) {
        oArray.some(res => {
          if (res.value == oParaValue) {
            this.checkval = false;
            this.mbIsLoading = true;
            this.deleteApiParam(oParaValue);
            this.parameterValue = '';
            return true
          }
          else {
            this.checkval = true;
            return false;
          }

        })
      }
      else {
        this.onOpenErrorBlock('Invalid Parameter');
        return;
      }
      if (this.checkval == true) {
        this.onOpenErrorBlock('Invalid Paramenter');
      }
    }
  }
  private deleteApiParam(oParaValue) {
    this.formService.DeleteReportParameter(this.msScreenName, oParaValue).subscribe(res => {
      if (res != null) {
        if (res.Response[0].ResponseText == "Success") {
          this.onOpenErrorBlock('Deleted Successfully.');
          //this.paramCtrl.nativeElement.value = "";
          this.parameterValue = '';
          this.clearModel();
          this.getParam(this.msScreenName);
          this.mbIsLoading = false;
          this.utilService.bmgfRefreshReuse('/main/accountspayable/1099-report', false);
          return true;
        }
      }

    })
  }
  onOpenErrorBlock(msg) {
    this.alertservice.infoAlert('Alert', '<div class="about-list">' + msg + '</div>');
  }
  onChangeEvent(e) {
    
    this.msSelectedPostType = e.target.id;
   if (e.target.checked == true && e.target.id == 'radioCopyA') {
     this.mobjFormData.CopyA = true;
     this.mobjFormData.Copy1=false;
     this.mobjFormData.CopyB=false;
     this.mobjFormData.Copy2=false;
     this.mobjFormData.CopyC=false;
   } else if (e.target.checked == true && e.target.id == 'radioCopy1') {
     this.mobjFormData.Copy1 = true;
     this.mobjFormData.CopyA = false;
     this.mobjFormData.CopyB=false;
     this.mobjFormData.Copy2=false;
     this.mobjFormData.CopyC=false;
   } else if (e.target.checked == true && e.target.id == 'radioCopyB') {
     this.mobjFormData.CopyB = true;
     this.mobjFormData.CopyA = false;
     this.mobjFormData.Copy1=false;
     this.mobjFormData.Copy2=false;
     this.mobjFormData.CopyC=false;
   } else if (e.target.checked == true && e.target.id == 'radioCopy2') {
     this.mobjFormData.Copy2 = true;
     this.mobjFormData.CopyA = false;
     this.mobjFormData.Copy1=false;
     this.mobjFormData.CopyB=false;
     this.mobjFormData.CopyC=false;
   } else if (e.target.checked == true && e.target.id == 'radioCopyC') {
    this.mobjFormData.CopyC = true;
     this.mobjFormData.CopyA = false;
     this.mobjFormData.Copy1=false;
     this.mobjFormData.CopyB=false;
     this.mobjFormData.Copy2=false;
   } else if(e.target.checked == true && e.target.id== 'chkUsePrePrintedStationery'){
     this.mbisUsePreprinted=true;
     this.mobjFormData.UsePrePrintedStationery=true;
   } else if(e.target.checked == false && e.target.id== 'chkUsePrePrintedStationery'){
    this.mbisUsePreprinted=false;
    this.mobjFormData.UsePrePrintedStationery=false;
  }
  console.log("checkbox value", this.mbisUsePreprinted)
  }

  
    
  onclose() {
    this.router.navigate(['main/dashboard']);
  }


    //Open Task MODAL
bmlfOpenTaskModal(taskType) {
  this.mobjTaskData = {};
  this.mobjTaskData.taskType = taskType;
  if (taskType == 'myTask') {
    this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
    this.mobjTaskData.innerRecord = this.mobjUserData;
  } else if (taskType == 'recordTask') {
    this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
    let pobjFormRec: any = {};
    pobjFormRec.formName = this.msScreenName;
    pobjFormRec.primeKey = "0";
    this.mobjTaskData.innerRecord = pobjFormRec;
  }
  this.mbIsTaskModal = true;
}

//Close Task MODAL
bmlfonCloseTaskModal(pobjData) {
  this.mbIsTaskModal = pobjData.isTaskModal;
}



}
