import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '../../../../core/services/alert.service';
import { ToastMessageService } from '../../../../core/services/toast-message.service';
import { AppUtility } from '../../../../core/services/utility.services';
import { LookupService } from '../../../../shared/http/lookup.service';
import { GridSettings } from 'src/app/core/models/grid-settings.interface';
import { TDSPaymentDetailModel, TDSPaymentModel2, TDSPaymentModel, TDSPaymentDetailModel2 } from '../../models/tds-payment-processing.model';
import { TdsPaymentProcessingService } from '../../http/tds-payment-processing.service';
import { timeout } from 'rxjs/operators';
import * as moment from 'moment';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { ModalService } from 'src/app/core/services/modal.service';
// import { getToday } from '@progress/kendo-angular-dateinputs/util';
import { SelectableSettings, DataStateChangeEvent, RowArgs } from '@progress/kendo-angular-grid';
import { CommonConstants } from 'src/app/core/constants/common.const';

@Component({
  selector: 'app-tds-payment-processing',
  templateUrl: './tds-payment-processing.component.html'
})
export class TdsPaymentProcessingComponent implements OnInit {
  public mObjModel: TDSPaymentModel = new TDSPaymentModel();
  public mObjModel2: TDSPaymentModel2 = new TDSPaymentModel2();
  public mobjLookupDataModel: TDSPaymentModel2 = new TDSPaymentModel2();
  public mObjModelPaymentDetail: TDSPaymentDetailModel = new TDSPaymentDetailModel();
  public mObjModelPaymentDetail2: TDSPaymentDetailModel2 = new TDSPaymentDetailModel2();
  public mObjModelCopyPaymentDetail2: TDSPaymentDetailModel2 = new TDSPaymentDetailModel2();

  public msScreenName: string = "TDS Payment Processing";
  public msRoutePath: string = '/main/accountspayable/tds-payment-processing';

  public mbisLookupModal: boolean = false;
  public mbIsLoading: boolean = false;
  public marrLookupGridData: any = [];
  public mobjLookupKeyData: any = {};
  public mbIsLookupModal: boolean = false;
  public mLookupDesc: any;
  public msLookUpKey: string = "";
  public mnumLineItemRowIndex: number = 0;
  public msCustomDateFormat: string = '';
  public mbLoadingEnabled: boolean = false;
  public pageSize: number = 10;

  public mbloaderEnabled: boolean = false;
  public mbShowDialog: boolean = false;
  public mbDescriptionDialog: boolean = false;
  public msRecUserID: string = '';
  public mbReportDialog: boolean = false;
  public msReportDl: string = '';
  public msAfterProcessMessage: any = {};
  public mnumTotalAmount: number = 0;
  public mnumTotalPayment: number = 0;
  public msBankName: string = '';
  public msAccountNo: string = '';
  public msCaptionPaymentBatchNo: string = 'Finance Series';
  public msCaptionPaymentMode: string = "Payment Mode";
  public msCaptionPaymentAccount: string = "Payment Account";
  public mobjSelectableSettings: SelectableSettings;
  public marrDetailGrid = [];
  public marrPaymentAmountGrid = [];
  public marrFullPaymentAmountGrid = [];
  public marrSelectedDetailGridRowIndx: number = 0;
  public marrSelectedDetailGrid = [];
  public marrSelectedPaymentAmtGrid = [];
  public marrDetailGridSettings: GridSettings = new GridSettings();
  public marrPaymentAmtGridSettings: GridSettings = new GridSettings();
  public allRowsSelected: boolean = false;
  public msHomeCurrency: string = '';
  public msHomeCurrencyDesc: string = '';
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  public mobjUserData: any = {};
  public msLookupField: string = "";

  public bmlfSelectedCallback(context: RowArgs): number {
    return context.dataItem.id;
  };

  constructor(private location: Location,
    private objService: TdsPaymentProcessingService,
    private localStorageService: LocalStorageService,
    private activeRoute: ActivatedRoute,
    private toastMessageService: ToastMessageService,
    private appUtility: AppUtility,
    private objLookupService: LookupService,
    public router: Router,
    private userService: UserFieldService,
    private dateFormatService: DateFormatService,
    private modalService: ModalService
  ) {
  }

  ngOnInit() {
    this.appUtility.getBMEScreePermission(this.msScreenName);
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.msRecUserID = this.localStorageService.getUserSession().UserId;
    this.mobjUserData = this.localStorageService.getUserSession();
    this.bmlfGetHomeCurrency();
    this.bmlfGetDefaultSeries();
  }

  bmlfGetDefaultSeries() {
    let pobjReqData = {
      ScreenName: 'PaymentProcessingComponent',
      LookUpKey: 'PaymentEntrySeries',
      Filter1: this.msRecUserID
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mObjModel2.PaymentBatchNo = res.LookupData[0].DefaultSeries;
      }
    });
  }

  bmlfonValidateFormData() {
    if (this.mObjModel2.PaymentBatchNo.trim() == '') {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TDSPAYMENTPROCESSING.MSG.ENTERPAYMENTBATCH')));
      setTimeout(function () { document.getElementById('txtPaymentBatchNo').focus(); }, 10);
      return false;
    }
    if (this.mObjModel2.PaymentMode.trim() == '') {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TDSPAYMENTPROCESSING.MSG.ENTERPAYMENTMODE')));
      setTimeout(function () { document.getElementById('txtPaymentMode').focus(); }, 10);
      return false;
    }
    if (this.mObjModel2.PaymentFromAC.trim() == '') {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TDSPAYMENTPROCESSING.MSG.ENTERPAYMENTACCT')));
      setTimeout(function () { document.getElementById('txtPaymentFromAC').focus(); }, 10);
      return false;
    }
    if (this.marrDetailGrid.length == 0 ) { //|| this.marrSelectedDetailGrid.length == 0
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TDSPAYMENTPROCESSING.MSG.NOLINESELECT')));
      return false;
    }
    return true;
  }


  bmlfonFromChange(value: Date) {
    let pbResult: boolean = true;
    pbResult = CustomValidator.bmgfCompareTwoDates(value, this.mObjModel.ToDate);
    if (pbResult == false) {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TDSPAYMENTPROCESSING.MSG.VALIDFROMDATE')));
      this.mObjModel.ToDate = new Date();
      this.mObjModel.FromDate = new Date();
      return false;
    }
  }

  bmlfonToChange(value: Date) {
    let pbResult: boolean = true;
    pbResult = CustomValidator.bmgfCompareTwoDates(this.mObjModel.FromDate, value);
    if (pbResult == false) {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TDSPAYMENTPROCESSING.MSG.VALIDTODATE')));
      this.mObjModel.ToDate = new Date();
      this.mObjModel.FromDate = new Date();
      return false;
    }
  }

  bmlfClearControl() {
    this.marrDetailGrid = [];
    this.marrFullPaymentAmountGrid = [];
    this.marrPaymentAmountGrid = [];
    this.marrDetailGridSettings = new GridSettings();
    this.marrPaymentAmtGridSettings = new GridSettings();
    this.mnumTotalAmount = 0;
    this.mnumTotalPayment = 0;
  }

  bmlfonProcess(type = 'B') {
    
    //check Write permission
    if (!this.appUtility.mbWrite) {
      this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.bmlfClearControl(); //Avanish 7Jul20 SCR:140110

    if (!this.appUtility.mbWrite) {
      this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    let psCustomDateFormat = CommonConstants.gsServerDateFormat; //this.msCustomDateFormat;
    let dateFrom = '';
    let dateTo = '';

    if (this.mObjModel.FromDate == null || this.mObjModel.FromDate == undefined) {
      dateFrom = moment(new Date(2000, 1, 1)).format(psCustomDateFormat.toUpperCase());
    }
    else {
      dateFrom = moment(this.mObjModel.FromDate).format(psCustomDateFormat.toUpperCase());
    }

    if (this.mObjModel.ToDate == null || this.mObjModel.ToDate == undefined) {
      dateTo = moment(new Date(2070, 1, 1)).format(psCustomDateFormat.toUpperCase());
    }
    else {
      dateTo = moment(this.mObjModel.ToDate).format(psCustomDateFormat.toUpperCase());
    }


    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          FromDate: dateFrom,
          ToDate: dateTo,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };

    this.objService.bmlfonGetTDSPayment(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.marrDetailGrid = res.TDSPaymentDtl;
        this.mObjModelPaymentDetail = res.TDSPaymentDtl;
        this.marrFullPaymentAmountGrid = res.VoucherPaymentDtl;
        if (this.marrFullPaymentAmountGrid.length > 0) {
          this.marrFullPaymentAmountGrid.forEach(ele => {
            ele.DocDate = moment(ele.DocDate).format(this.msCustomDateFormat.toUpperCase());
          });
        }
        if (type == 'B' && this.marrDetailGrid.length == 0) {
          this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TDSPAYMENTPROCESSING.MSG.NODATA')));
        }
        this.bmlfonAmtChange(1);
        this.bmlfonInitGridSettings();

      }
      else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
        this.marrDetailGrid = [];
        this.marrPaymentAmountGrid = [];
      }
    });
  }

  bmlfonPaymentAmtGridListChange(e) {
    
    this.bmlfonAmtChange(2);
  }

  bmlfonDetailGridListChange(e) {
    
      this.marrPaymentAmountGrid = [];
      this.marrSelectedDetailGridRowIndx = e.rowIndex;
      let psTDSType = e.dataItem.TDSType;
      if (this.blfIsRowExist(psTDSType)) {
        this.bmlfonAmtChange(2);
        this.bmlfonInitPaymentGridSettings();
        this.modalService.open('TDSPaymentDetail');
        this.allRowsSelected = false;
      }
  }

  blfIsRowExist(TDSType): any {
    
    this.marrPaymentAmountGrid = [];
    this.marrPaymentAmtGridSettings = new GridSettings();
    this.marrPaymentAmountGrid = this.marrFullPaymentAmountGrid.filter(x => x['TDSType'].toUpperCase() == TDSType.toUpperCase());
    this.marrPaymentAmountGrid.forEach(ele => {
      if (ele.Select == true) {
        ele.checked = true;
      }
    });
    this.bmlfonInitPaymentGridSettings();
    return true;
  }

  bmlfonCloseDetail(psId: any, action: any) {
    if (action == 'close') {
      this.modalService.close(psId);
    }
  }

  bmlfonOkDetail(psId: any, action: any) {
    
    if (action == 'ok') {
      if (this.marrSelectedDetailGridRowIndx >= 0) {
        this.marrPaymentAmountGrid.forEach((ele) => {
          ele.Select = ele.checked;
          this.bmlfSelFullPaymentGridData(ele);
        });

        this.marrDetailGrid[this.marrSelectedDetailGridRowIndx].PayableAmount = this.mnumTotalPayment;
        this.marrDetailGrid[this.marrSelectedDetailGridRowIndx].CreatedUserID = this.msRecUserID;
        this.marrDetailGrid[this.marrSelectedDetailGridRowIndx].ModifyUserID = this.msRecUserID;
        this.marrDetailGrid[this.marrSelectedDetailGridRowIndx].CreateDate = new Date();
        this.marrDetailGrid[this.marrSelectedDetailGridRowIndx].ModifyDate = new Date();
      }
      this.bmlfonAmtChange(1);
      this.modalService.close(psId);
    }
  }

  bmlfSelFullPaymentGridData(ele) {
    this.marrFullPaymentAmountGrid.forEach((e) => {
      if ((ele.TDSType == e.TDSType) && (ele.DocNo == e.DocNo) && (ele.RowNum == e.RowNum)) {
        e.Select = ele.Select;
      }
    });
  }

  selectAllRows(e): void {
    if (e.target.checked) {
      this.allRowsSelected = true;
      this.marrPaymentAmountGrid.forEach((ele) => {
        ele.checked = true;
        ele.Select = true;
      });
      this.bmlfonAmtChange(2);
    } else {
      this.allRowsSelected = false;
      this.marrPaymentAmountGrid.forEach((ele) => { ele.checked = false; ele.Select = false; });
      this.bmlfonAmtChange(2);
    }
  }

  selectSingleRow(e) {
    
    this.allRowsSelected = false;
    this.bmlfonAmtChange(2);
  }

  bmlfonAmtChange(type) {
    if (type == 1) {
      this.mnumTotalAmount = 0;
      this.marrDetailGrid.forEach(ele => {
        this.mnumTotalAmount = this.mnumTotalAmount + ele.PayableAmount;
      });
    } else if (type == 2) {
      this.marrSelectedPaymentAmtGrid = [];
      this.marrSelectedPaymentAmtGrid = this.marrPaymentAmountGrid.filter(ele => ele.checked == true);
      this.mnumTotalPayment = 0;
      this.marrSelectedPaymentAmtGrid.forEach(ele => {
        this.mnumTotalPayment = this.mnumTotalPayment + ele.TDSAmt;
      });
    }
  }

  bmlfonInitGridSettings() {
    const parrColumnList = Object.keys(this.marrDetailGrid[0]);
    parrColumnList.forEach(ele => {
      let pobjCol: any = {
        field: ele,
        title: ele,
        filter: '',
        type: 'text',
        filterable: true,
        hidden: false
      }
      if (ele == 'TDSType' || ele == 'TDSRate' || ele == 'Acct' || ele == 'PayableAmount') {
      } else {
        pobjCol.hidden = true;
      }

      pobjCol.title = this.appUtility.bmgfonTranslate("TDSPAYMENTPROCESSING." + pobjCol.title.toUpperCase() + "");

      this.marrDetailGridSettings.columnsConfig.push(pobjCol);
    });
  }

  bmlfonInitPaymentGridSettings() {
    if (this.marrPaymentAmtGridSettings.columnsConfig.length == 0) {
      const parrColumnList = Object.keys(this.marrPaymentAmountGrid[0]);
      parrColumnList.forEach(ele => {
        let pobjCol: any = {
          field: ele,
          title: ele,
          filter: '',
          type: 'text',
          filterable: true,
          hidden: false
        }

        if (ele == 'DocNo' || ele == 'RowNum' || ele == 'DocDate' || ele == 'VendKey' || ele == 'VendName' || ele == 'DocTotal' || ele == 'TDSAmt') {
        } else {
          pobjCol.hidden = true;
        }

        pobjCol.title = this.appUtility.bmgfonTranslate("TDSPAYMENTPROCESSING." + pobjCol.title.toUpperCase() + "");

        this.marrPaymentAmtGridSettings.columnsConfig.push(pobjCol);
      });
    }
  }

  bmlfGetHomeCurrency() {
    let pobjReqData = {
      ScreenName: 'PaymentProcessingComponent',
      LookUpKey: 'CompanyInfo',
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.msHomeCurrency = res.LookupData[0].HomeCurrency;
        this.msHomeCurrencyDesc = res.LookupData[0].CurrencyDescription;
      }
    });

  }

  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }

  //Lookup
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    
    if (psEvent == 'blur' && this.msLookupField == psValue) {
      this.msLookupField = '';
      return false;
    }

    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjLookupDataModel[psField] = "";
      return false;
    }
    let psReqData: any;

    if (psLookUpKey == "PaymentFromAccount") {
      psReqData = { "ScreenName": this.msScreenName, "LookUpKey": psLookUpKey, "Filter1": this.msHomeCurrency, "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    } else if (psLookUpKey == "PaymentMode") {
      psReqData = { "ScreenName": this.msScreenName, "LookUpKey": psLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    }

    this.objLookupService.getLookup(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        let psLookupHeading = psField;
        if (psLookUpKey == "PaymentMode") {
          res.LookupData.forEach(ele => {
            ele.RecDate = moment(ele.RecDate).format(this.msCustomDateFormat.toUpperCase());
          });
        }
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }

        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    });
  }

  bmlfOpenCustomLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {

    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjLookupDataModel[psField] = "";
      return false;
    }
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          ScreenName: 'Payment Entry',//this.msScreenName,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };

    this.objService.bmlfonGetFinanceSeries(reqData).subscribe(res => {
      if (res.SeriesData.length > 0) {
        this.marrLookupGridData = res.SeriesData;

        this.mobjLookupKeyData.field = 'Finance Series';
        //this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mbIsLookupModal = true;
      }
    });
  }

  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate(psMsg));
  }

  bmlfSaveLookupField(pobjEvent) {
    this.mbIsLookupModal = false;

    if (pobjEvent.field == this.msCaptionPaymentAccount) {
      this.mObjModel2.PaymentFromAC = pobjEvent.value["BMEAcct"].trim();
      this.msBankName = pobjEvent.value["BankName"].trim();
      this.msAccountNo = pobjEvent.value["BankAcNo"].trim();
      this.mObjModel2.CurrencyKey = pobjEvent.value["CurrencyKey"].trim();
      this.mObjModel2.BankCashID = pobjEvent.value["BankId"];
    } else if (pobjEvent.field == this.msCaptionPaymentMode) {
      this.mObjModel2.PaymentMode = pobjEvent.value["PaymentMode"].trim();
    } else if (pobjEvent.field == "Finance Series") {
      this.mObjModel2.PaymentBatchNo = pobjEvent.valueKey;
    }
  }

  bmlfCloseLookupModal(pobjEvent): void {
    this.mbIsLookupModal = pobjEvent.isLookupModal;
    document.getElementById(pobjEvent.eleId).focus();
  }

  RemoveUnusedRows() {
    let pRowIndex: number = 0;
    let pDuplicateRowIndex = [];
    if (this.marrPaymentAmountGrid.length > 0)
      this.marrPaymentAmountGrid.forEach(ele => {
        if (ele.Select == false || ele.Select == undefined) {
          pDuplicateRowIndex.push(pRowIndex);
        }
        ++pRowIndex;
      });

    while (pDuplicateRowIndex.length) {
      this.marrPaymentAmountGrid.splice(pDuplicateRowIndex.pop(), 1);
    }
  }

  GetSelectedRows() {
    this.marrDetailGrid.forEach(ele => {
      if (ele.PayableAmount > 0) {
        this.marrSelectedDetailGrid.push(ele);
      }
    });
  }

  // Report expanded Modal
  bmlfReportModal(psKey) {
    
    if (psKey == 'open') {
      this.mbReportDialog = true;
    } else if (psKey == 'close') {
      this.mbReportDialog = false;
      this.bmlfClearControl();
    } else if (psKey == 'ok') {
      this.mbReportDialog = false;
      this.bmlfClearControl();
    }
  }

  bmlfonPost() {
    
    //check Write permission
    if (!this.appUtility.mbWrite) {
      this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    let pbResult: boolean = false;
    // this.GetSelectedRows();
    pbResult = this.bmlfonValidateFormData();

    if (pbResult == true) {
      //this.RemoveUnusedRows();

      this.mObjModel2.TotalAmount = this.mnumTotalAmount;
      let parrPaymentGrid = this.marrFullPaymentAmountGrid.filter(x => x['Select'] == true);
      let parrDetailGrid = this.marrDetailGrid.filter(x => x['PayableAmount'] > 0);
      if (parrPaymentGrid.length == 0) {
        this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TDSPAYMENTPROCESSING.MSG.NOLINESELECT')));
        this.bmlfonProcess('A');
        return false;
      }
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{ ...this.mObjModel2 }],
          DetailData: parrDetailGrid, //[{ ...this.marrDetailGrid[this.marrSelectedDetailGridRowIndx] }],
          DetailData1: parrPaymentGrid, //this.marrPaymentAmountGrid,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };

      this.objService.bmlfonPostTDSPayment(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          //this.toastMessageService.notifySuccess(res.Response[0].ResponseText);
          this.msReportDl = res.Response[0].ResponseText;
          this.msAfterProcessMessage.screenName = this.msScreenName;
          this.msAfterProcessMessage.Data = res.Response[0].ResponseText;
          if (this.msReportDl.length > 0) {
            this.bmlfReportModal('open');
            this.mbReportDialog = true;

          }
        }
        else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
          this.bmlfClearControl();
        }
      });
    } else {
      //this.bmlfonProcess();
    }
  }

  bmlfonClose() {
    this.router.navigate(['/main/accountspayable/refresh']);
  }

  bmlfonDataStateChange(state: DataStateChangeEvent): void {
    // this.currentState = state;
    // this.filterTrue = true;
    // this.allData = this.allData.bind(this);
    // this.marrGridSettings.state = state;
  }

  // Description expanded Modal
  bmlfDescriptionModal(event) {
    if (event == 'open') {
      this.mbDescriptionDialog = true;
    } else if (event == 'close') {
      this.mbDescriptionDialog = false;
    }
  }

  //Open Task MODAL
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.appUtility.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.appUtility.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = "";
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  //close report modal
  bmlfOnRptAction(event) {
    this.mbReportDialog = false;
    this.bmlfClearControl();
}
}
