  bmlfGetEntry(EntryType) {

    this.msUnposted = EntryType == "Post" ? "Posted" : "Unposted";
    if (this.msUnposted == "Unposted") {
      this.mbUnPosted = true;
    } else if (this.msUnposted == "Posted") {
      this.mbUnPosted = false;
    }
    this.getDataList('filterd');
  }
  //print 
  // Single Print
  bmlfonPrint(dataItem: any) {
    let psPosted: string = 'POSTED';
    if (dataItem.Status == 'NEW' || dataItem.Status == 'HOLD' || dataItem.Status == 'VOID') {
      psPosted = 'UNPOSTED';
    }


    try {
      if (this.msScreenName == 'Vendor Debit Note') {
        let printReqParam: any = {
          "ReportName": 'Vendor Debit Note Report',
          "VendorKeyFrom": dataItem.Vendkey,
          "VendorKeyTo": dataItem.Vendkey,
          "Criteria": psPosted,
          "DebitNoteNoFrom": dataItem.DebitNoteNo,
          "DebitNoteNoTo": dataItem.DebitNoteNo,
          //"NumberFrom": dataItem.vouchno,
          //"NumberTo": dataItem.vouchno,
          "DateFrom": moment(this.objUtil.bmlfSetDateAsDateFormat(dataItem.Date)).format(CommonConstants.gsServerDateFormat),
          "DateTo": moment(this.objUtil.bmlfSetDateAsDateFormat(dataItem.Date)).format(CommonConstants.gsServerDateFormat),
          "ListingFlag": false,
          "ListingVariable": ""
        }
        this.utilService.bmgfReportAction(printReqParam, 'Vendor Debit Note Report').then((value) => {
          this.maarraySelected = [];
          this.getDataList();
        });
      }
      else {
        let printReqParam: any = {
          "ReportName": 'AP Voucher Report',
          "VendorKeyFrom": dataItem.Vendkey,
          "VendorKeyTo": dataItem.Vendkey,
          "Criteria": psPosted,
          "APVoucherFrom": dataItem.APVoucherNo,
          "APVoucherTo": dataItem.APVoucherNo,
          "DateFrom": moment(this.objUtil.bmlfSetDateAsDateFormat(dataItem.Date)).format(CommonConstants.gsServerDateFormat),
          "DateTo": moment(this.objUtil.bmlfSetDateAsDateFormat(dataItem.Date)).format(CommonConstants.gsServerDateFormat),
          "ListingFlag": false,
          "ListingVariable": ""
        }
        this.utilService.bmgfReportAction(printReqParam, 'AP Voucher Report').then((value) => {
          this.maarraySelected = [];
          this.getDataList();
        });
      }


    } catch (e) {
      console.log(e);
    }
  }

  // Multiple Print
  onActionPrint() {

    let pnReceiptType: number = 0;
    let psPrintAll: string = '';
    let piCnt: number = 0;
    let pdDateFrom = new Date();
    let pdDateTo = new Date();
    this.maarraySelected.forEach((ele) => {
      if (piCnt > 0) {
        if (this.msScreenName == 'Vendor Debit Note') {
          psPrintAll = psPrintAll + '—' + ele.DebitNoteNo;
        }
        else {
          psPrintAll = psPrintAll + '—' + ele.APVoucherNo;
        }

        pdDateTo = ele.Date;
      } else {
        if (this.msScreenName == 'Vendor Debit Note') {
          psPrintAll = ele.DebitNoteNo;
        }
        else {
          psPrintAll = ele.APVoucherNo;
        }

        pdDateFrom = ele.Date;
      }
      ++piCnt;
    });
    try {
      if (this.msScreenName == 'Vendor Debit Note') {
        let printReqParam: any = {
          "ReportName": 'Vendor Debit Note Report',
          "VendorKeyFrom": '',
          "VendorKeyTo": '',
          "Criteria": this.msUnposted.toUpperCase(),
          "DebitNoteNoFrom": '',
          "DebitNoteNoTo": '',
          "NumberFrom": '',
          "NumberTo": '',
          "DateFrom": '1-Jan-1900',
          "DateTo": '1-Jan-3001',
          "ListingFlag": true,
          "ListingVariable": psPrintAll
        }
        this.utilService.bmgfReportAction(printReqParam, 'Vendor Debit Note Report').then((value) => {
          this.maarraySelected = [];
          this.getDataList();
        });
      }
      else {
        let printReqParam: any = {
          "ReportName": 'AP Voucher Report',
          "VendorKeyFrom": '',
          "VendorKeyTo": '',
          "Criteria": this.msUnposted.toUpperCase(),
          "APVoucherFrom": '',
          "APVoucherTo": '',
          "DateFrom": '1-Jan-1900',
          "DateTo": '1-Jan-3001',
          "ListingFlag": true,
          "ListingVariable": psPrintAll
        }
        this.utilService.bmgfReportAction(printReqParam, 'AP Voucher Report').then((value) => {
          this.maarraySelected = [];
          this.getDataList();
        });
      }


    } catch (e) {
      console.log(e);
    }
  }

