import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { AccountPayableComponent } from './account-payable.component';
import { PaymentEntryListComponent } from './components/payment-entry/payment-entry-list/payment-entry-list.component';
import { PaymentEntryManageComponent } from './components/payment-entry/payment-entry-manage/payment-entry-manage.component';
import { VendorDebitNoteListComponent } from './components/vendor-debit-note/vendor-debit-note-list/vendor-debit-note-list.component';
import { VendorDebitNoteManageComponent } from './components/vendor-debit-note/vendor-debit-note-manage/vendor-debit-note-manage.component';
import { WriteoffVendorDocumentsComponent } from './components/writeoff-vendor-documents/writeoff-vendor-documents.component';
import { PaymentRangeProcessingListComponent } from './components/payment-range-processing/payment-range-processing-list/payment-range-processing-list.component';
import { PaymentRangeProcessingManageComponent } from './components/payment-range-processing/payment-range-processing-manage/payment-range-processing-manage.component';
import { ReprintCheckComponent } from './components/reprint-check/reprint-check.component';
import { TdsPaymentProcessingComponent } from './components/tds-payment-processing/tds-payment-processing.component';
import { ApplyPaymentComponent } from './components/apply-payment/apply-payment.component';
import { EditInformationComponent } from './components/edit-information/edit-information.component';
import { OpeningBalanceTransactionComponent } from './components/opening-balance-transaction/opening-balance-transaction.component';
import { VendorAgingComponent } from './components/report/vendor-aging/vendor-aging.component';
import { VendorAgingDashboardComponent } from './components/report/vendor-aging/vendor-aging-dashboard.component';
import { VendorAgingDashboardDetailComponent } from './components/report/vendor-aging/vendor-aging-dashboard-detail.component';
import { PaymentReportComponent} from './components/report/payment-report/payment-report.component';
import { VendorDebitNoteReportComponent} from './components/report/vendor-debit-note-report/vendor-debit-note-report.component'
import { OnezeroEnquiryComponent } from './components/onezero-enquiry/onezero-enquiry.component';
import {APVoucherReportComponent} from './components/report/apvoucher-report/apvoucher-report.component';
import {Report1099Component} from './components/report/report1099/report1099.component';
import {ApplyPaymentReportComponent} from './components/report/apply-payment-report/apply-payment-report.component';
const routes: Routes = [
    {
        path: '',
        component: AccountPayableComponent,
        children: [
            { path: '', redirectTo: 'accountspayable', pathMatch: 'full' },
            { path: 'payment-entry', component: PaymentEntryListComponent, canActivate: [AuthGuard] },
            { path: 'payment-entry/manage', component: PaymentEntryManageComponent, canActivate: [AuthGuard] },
            { path: 'vendor-debit-note', component: VendorDebitNoteListComponent, canActivate: [AuthGuard] },
            { path: 'vendor-debit-note/manage', component: VendorDebitNoteManageComponent, canActivate: [AuthGuard] },
            { path: 'apvoucher', component: VendorDebitNoteListComponent, canActivate: [AuthGuard] },
            { path: 'apvoucher/manage', component: VendorDebitNoteManageComponent, canActivate: [AuthGuard] },
            { path: 'writeoffvendordocuments', component: WriteoffVendorDocumentsComponent, canActivate: [AuthGuard] },
            { path: 'payment-range-processing', component: PaymentRangeProcessingListComponent, canActivate: [AuthGuard] },
            { path: 'payment-range-processing/manage', component: PaymentRangeProcessingManageComponent, canActivate: [AuthGuard] },
            { path: 'reprint-check', component: ReprintCheckComponent, canActivate: [AuthGuard] },
            { path: 'tds-payment-processing', component: TdsPaymentProcessingComponent, canActivate: [AuthGuard] },
            { path: 'apply-payment', component: ApplyPaymentComponent, canActivate: [AuthGuard] },
            { path: 'edit-1099-information', component: EditInformationComponent, canActivate: [AuthGuard] },
            { path: '1099-opening-balance-transaction', component: OpeningBalanceTransactionComponent, canActivate: [AuthGuard] },
            { path: 'vendor-aging', component: VendorAgingComponent, canActivate: [AuthGuard] },
            { path: 'vendor-aging-dashboard', component: VendorAgingDashboardComponent, canActivate: [AuthGuard] },
            { path: 'vendor-aging-dashboard-detail', component: VendorAgingDashboardDetailComponent, canActivate: [AuthGuard] },
            { path: 'payment-report', component:PaymentReportComponent, canActivate:[AuthGuard]},
            { path: 'vendor-debit-not-report', component:VendorDebitNoteReportComponent, canActivate:[AuthGuard]},
            { path: '1099-enquiry', component:OnezeroEnquiryComponent, canActivate:[AuthGuard]},
            {path:'ap-voucher-report', component:APVoucherReportComponent, canActivate:[AuthGuard]},
            {path:'1099-report', component:Report1099Component, canActivate:[AuthGuard]},
            {path:'apply-payment-report', component:ApplyPaymentReportComponent, canActivate:[AuthGuard]}
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AccountPayableRoutingModule { }




