import { Component, OnInit, Renderer2 } from '@angular/core';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { Router } from '@angular/router';
import { GridSettings } from 'src/app/core/models/grid-settings.interface';
import { DataStateChangeEvent } from '@progress/kendo-angular-grid';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { JsonPipe } from '@angular/common';
import { ToastMessageService } from '../../../../../core/services/toast-message.service';
import { PaymentRangeProcessingModel } from '../../../models/payment-range-processing.model';
import { PaymentRangeProcessingService } from '../../../http/payment-range-processing.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { PaymentRangeProcessingGrid1Constants } from '../../../constant/payment-range-processing.Constants';

@Component({
  selector: 'app-payment-range-processing-list',
  templateUrl: './payment-range-processing-list.component.html'
})

export class PaymentRangeProcessingListComponent extends BaseGrid implements OnInit {
  public bmlfSelectedCallback = (args) => args.dataItem;
  public gridDataList = [];
  public orginalGridList = [];
  public mbloadingEnabled = false;
  public gridSettings: GridSettings = new GridSettings();
  public mscustomDateFormat: string = "";
  public mbShowDeleteDialog: boolean = false;
  public mObjModel: PaymentRangeProcessingModel = new PaymentRangeProcessingModel();
  public msPaymentKey: string = "";
  public maarraySelected: Array<any> = [];
  public msLookupKey: string = "PaymentRangeProcessingUNPOSTED";
  public mbBulkDelete: boolean = false;
  public msScreenName: string = 'Payment Range Processing';
  public msMsgData: any = {};
  public mbBulkDeleteView: boolean = false;
  public mbUnPosted: boolean = true;
  public msUnposted: string = "Unposted";
  public msUnpostedcount: string = "Unposted Entry";
  public msPostedcount: string = "Posted Entry";

  constructor(private router: Router,
    public renderer: Renderer2,
    private lsService: LocalStorageService,
    private PaymentRangeProcessingService: PaymentRangeProcessingService,
    public dateFormatService: DateFormatService, public objToastService: ToastMessageService,
    public objUtil: AppUtility,
    public userFieldService: UserFieldService) {
    super(lsService, dateFormatService, userFieldService, objToastService, objUtil);
  }
  //---------------------------------------------------------------------------------------------
  ngOnInit() {
    
    this.mscustomDateFormat = this.dateFormatService.getDateFormat();
    this.initGrid();
    this.objUtil.getBMEScreePermission(this.msScreenName);
    this.msUnposted = "Unposted";
    this.getDataList();
    this.setExportData({ 'pageName': this.msScreenName });
  }

  bmlfGetEntry(EntryType) {
    
    this.msUnposted = EntryType;
    if (this.msUnposted == "Unposted") {
      this.mbUnPosted = true;
    } else if (this.msUnposted == "Posted") {
      this.mbUnPosted = false;
    }
    this.getDataList();
  }
  //---------------------------------------------------------------------------------------------    
  getDataList(): void {
    
    this.mbloadingEnabled = true;
    let EntryType: string = this.msUnposted;
    if (EntryType == 'Posted') {
      this.msLookupKey = 'PaymentRangeProcessingPOSTED';
    } else {
      this.msLookupKey = 'PaymentRangeProcessingUNPOSTED';
    }
    const headerData = { "ScreenName": this.msScreenName, "LookUpKey": this.msLookupKey, "Filter1": "BOTH", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" }

    this.PaymentRangeProcessingService.bmlfonGetPaymentRangeList(headerData).subscribe(

      res => {
        let parrGridData = res.LookupData;
        parrGridData.forEach(ele => {
          ele.Recdate = new Date(ele.Recdate);
          ele.ApplyDate = new Date(ele.ApplyDate);
          ele.PaymentDate = new Date(ele.PaymentDate);
          ele.PostingDate = new Date(ele.PostingDate);
          ele.PaymentDocDate = new Date(ele.PaymentDocDate);
        })

        this.gridDataList = parrGridData;
        this.orginalGridList = parrGridData;
        let parrSettingData = res.GridSettingData[0].JSONString;
        this.setInitGridSettings(parrSettingData, "PAYMENTRANGEPROCESSING");
        this.mobjGridSettings.state.take=50;
        // this.setInitGridSettings("", "PAYMENTRANGEPROCESSING");
        this.bmlfGetFiltersCounts();
      }
      , error => {
        console.log(error);
      }

    ).add(() => { this.mbloadingEnabled = false })
  }

  bmlfGetFiltersCounts() {
    let parrReqData = [{
      ScreenName: "Payment Range Processing",
      LookUpKey: "PRPPostedCount"
    }, {
      ScreenName: "Payment Range Processing",
      LookUpKey: "PRPUnpostedCount"
    }]
    let apiName = '/bme/GETGeneralSqlData';
    this.userFieldService.bmlfRunMultipleAPI(parrReqData, apiName).subscribe(res => {
      let resList = [];
      res.forEach(ele => {
        if (ele.LookupData[0].RecCount) {
          let convToJson = ele.LookupData[0].RecCount;
          resList.push(JSON.parse(convToJson));
        }
      });

      resList.forEach(ele => {
        if (ele.Field == "UnPost") {
          this.msUnpostedcount = "Unposted Entry (" + String(ele.Count) + ")";
        }
        if (ele.Field == "Post") {
          this.msPostedcount = "Posted Entry (" + String(ele.Count) + ")";
        }

      });
    }, err => {
      console.log(err);
    });
  }
  //---------------------------------------------------------------------------------------------
  bmlfResetGridSettings() {
    this.bmlfOnResetGridSettings(this.msScreenName, "PAYMENTRANGEPROCESSING");
  }
  //---------------------------------------------------------------------------------------------    
  bmlfSaveGridSetting(): void {
    this.bmlfOnSaveGrid(this.msScreenName);
  }
  //---------------------------------------------------------------------------------------------
  //Add
  bmlfonAddPaymentRange(): void {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    this.router.navigate(['/main/accountspayable/payment-range-processing/manage']);

  }
  //---------------------------------------------------------------------------------------------
  //Edit
  bmlfonEdit(data) {
    this.router.navigate(['/main/accountspayable/payment-range-processing/manage'], { queryParams: { DocNo: data.DocNo } });
  }
  //---------------------------------------------------------------------------------------------
  //copy
  bmlfonCopy(data) {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate(['/main/accountspayable/payment-range-processing/manage'], { queryParams: { DocNo: data.DocNo, CopyRecord: "Y" }, skipLocationChange: true });
  }
  //---------------------------------------------------------------------------------------------
  //Delete record
  bmlfonDelete(data) {

    if (data.Status != 'NEW') {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('PAYMENTENTRY.MSG.DELETESTATSCHECK'));
      return;
    }

    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };

    this.msPaymentKey = data.DocNo;
    this.mObjModel.DocNo = this.msPaymentKey
    this.mbShowDeleteDialog = true;
  }
  //---------------------------------------------------------------------------------------------
  //Bulk Delete
  onActionDelete() {
    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };

    if (this.maarraySelected.length == 0) {
      this.objToastService.notifyError(this.objUtil.bmgfonTranslate('COMMON.MSG.NODATAFORDELETE'))
      return;
    }
    //  
    // var i;
    // for (i = 0; i < this.maarraySelected.length; i++) {
    //   if (this.maarraySelected[i].Status.toUpperCase() != 'NEW') {
    //     this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('PAYMENTRANGEPROCESSING.MSG.DELETESTATSCHECK'));
    //     return;
    //   }
    //}


    this.mbBulkDelete = true;
    this.mbShowDeleteDialog = true;
  }
  //---------------------------------------------------------------------------------------------
  bmlfonfilterEvent(obj) {
    if (obj.Type == "String") {
      let filterGrid = this.orginalGridList.filter(x => x[obj.FirstKey] == obj.FirstValue);
      this.gridDataList = filterGrid;
    }
    else if (obj.Type == "Date") {
      let filterGrid = this.orginalGridList.filter(x => (new Date(x[obj.FirstKey]).getTime() >= obj.FirstValue.getTime()) && new Date(x[obj.FirstKey]).getTime() <= obj.SecondValue.getTime());
      this.gridDataList = filterGrid;
    }
  }
  //---------------------------------------------------------------------------------------------
  bmlfonDialogAction(action) {
    
    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {

      if (this.mbBulkDelete == true) {
        let pArrNewStatusRecord = this.maarraySelected.filter(x => x['Status'] == 'NEW');
        let pArrOtherStatusRecord = this.maarraySelected.filter(x => x['Status'] != 'NEW');

        let reqData: any = {
          JSON: JSON.stringify({
            HeaderData: pArrNewStatusRecord, //this.maarraySelected,
            Response: [{
              "ResponseText": "", "ErrorCode": ""
            }]
          })
        };
        this.PaymentRangeProcessingService.bmlfonDDeletePaymentRangeProcess(reqData).subscribe(res => {
          if (res == null) {
            this.objToastService.notifyError('Server Error');
            return false;
          }
          if (res.Response[0].ErrorCode == "") {           
              //this.objToastService.notifySuccess(res.Response[0].ResponseText);
              this.msMsgData.Data = res.Response[0].ResponseText;
              if (pArrOtherStatusRecord.length > 0) {
                //Avanish 21Aug20 SCR:141644
                pArrOtherStatusRecord.forEach(ele => {
                  this.msMsgData.Data += '<br>' + ele.DocNo + ' - ' + this.objUtil.bmgfonTranslate('PAYMENTRANGEPROCESSING.MSG.DELETESTATSCHECK');
                })
              }
              this.msMsgData.Header = "BatchMaster";
              this.msMsgData.mbBulkDeleteView = true;
              this.mbBulkDeleteView = true;

              this.mbShowDeleteDialog = false;
              this.mbBulkDelete = false;
              this.maarraySelected = [];
              this.getDataList();
            
          } else {                   
              this.msMsgData.Data = res.Response[0].ResponseText;
              this.msMsgData.Header = "BatchMaster";
              this.msMsgData.mbBulkDeleteView = true;
              this.mbBulkDeleteView = true;
              this.mbShowDeleteDialog = false;
              this.mbBulkDelete = false;
              this.maarraySelected = [];
            }            
        })
      }
      else {

        let reqData: any = {
          JSON: JSON.stringify({
            HeaderData: [{ ... this.mObjModel }],
            Response: [{
              "ResponseText": "", "ErrorCode": ""
            }]
          })

        };
        this.PaymentRangeProcessingService.bmlfonDDeletePaymentRangeProcess(reqData).subscribe(res => {
          console.log('in rspns', res)
          if (res == null) {
            console.log('in res null')

            this.objToastService.notifyError('Server Error');
            return false;
          }
          if (res.Response[0].ErrorCode == "") {
          
            {
              if (res.Response[0].ResponseText.search("Reference") != -1) {               
                this.objToastService.notifyError(this.objUtil.bmgfonTranslate('COMMON.MSG.EXIST_REF'));
                this.mbShowDeleteDialog = false;
                this.maarraySelected = [];
                return;
              }
              else if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {             
                this.objToastService.notifySuccess((this.objUtil.bmgfonTranslate('COMMON.MSG.DELETE')));
                this.mbShowDeleteDialog = false;
                this.mbBulkDelete = false;
                this.maarraySelected = [];
                this.getDataList();
              } else {               
                this.objToastService.notifyError(res.Response[0].ResponseText)
                this.mbShowDeleteDialog = false;
                this.maarraySelected = [];
              }
            }
          } else {        
            this.objToastService.notifyError(res.Response[0].ResponseText);
            this.mbShowDeleteDialog = false;
            this.maarraySelected = [];
          }
        })
      }

    }
  }
  //-----------------------------------------------------------------------------------------
  bmlfonselectionChange(e) {

    if (e.selectedRows.length > 0) {
      //this.maarraySelected = [...this.maarraySelected, ...e.selectedRows];
      e.selectedRows.forEach(ele => {
        this.maarraySelected.push(ele.dataItem);
      })

    }
    else if (e.deselectedRows.length > 0) {
      // let indexArray = e.deselectedRows.map(data => data.index);
      // this.maarraySelected = this.maarraySelected.filter(element => {
      //   return !indexArray.includes(element.index)
      // })
      e.deselectedRows.forEach(ele => {
        let pobjTempData = ele.dataItem;
        this.maarraySelected = this.maarraySelected.filter(x => {
          if (x.PaymentNo != pobjTempData.PaymentNo) {
            return x;
          }
        })
      })
      console.log(this.maarraySelected);
    }
  }
  //--------------------------------------------------------------------------------------------
  public bmlfoncloseModal(event) {
    this.mbBulkDeleteView = event.mbBulkDeleteView;
  }
  //--------------------------------------------------------------------------------------------
  pageChange(e) {
    this.maarraySelected = [];
  }
  //--------------------------------------------------------------------------------------------
  bmlfonPost(dataItem, bulkFlg) {
    

    if (bulkFlg) {
      this.mbBulkDelete = true;
    } else {
      this.mbBulkDelete = false;
    }


    if (this.mbBulkDelete == true) {


      if (this.maarraySelected.length == 0) {
        this.objToastService.notifyError(this.objUtil.bmgfonTranslate('PAYMENTRANGEPROCESSING.MSG.NODATAPOST'))
        return;
      }

      let pArrNewStatusRecord = this.maarraySelected.filter(x => x['Status'] == 'NEW');
      let pArrOtherStatusRecord = this.maarraySelected.filter(x => x['Status'] != 'NEW');

      if (pArrNewStatusRecord.length == 0) {
        this.msMsgData.Data = this.objUtil.bmgfonTranslate('PAYMENTRANGEPROCESSING.MSG.POSTSTATSCHECK');
        this.msMsgData.Header = "BatchMaster";
        this.msMsgData.mbBulkDeleteView = true;
        this.mbBulkDeleteView = true;

        this.mbShowDeleteDialog = false;
        this.mbBulkDelete = false;
        this.maarraySelected = [];
        return;
      }

      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: pArrNewStatusRecord, //this.maarraySelected,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.PaymentRangeProcessingService.bmlfonPostPaymentRangeProcess(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          //this.msReport = res.Response[0].ResponseText;
          //this.mbReportDialog = true;
          this.msMsgData.Data = res.Response[0].ResponseText;
          if (pArrOtherStatusRecord.length > 0) {
            this.msMsgData.Data += '<br>' + this.objUtil.bmgfonTranslate('PAYMENTRANGEPROCESSING.MSG.POSTSTATSCHECK');
          }
          this.msMsgData.Header = "BatchMaster";
          this.msMsgData.mbBulkDeleteView = true;
          this.mbBulkDeleteView = true;

          this.mbShowDeleteDialog = false;
          this.mbBulkDelete = false;
          this.maarraySelected = [];
          this.getDataList();
        } else {
          if (res.Response[0].ResponseText.length > 80) {
            //this.msReport = res.Response[0].ResponseText;
            //this.mbReportDialog = true;
            this.msMsgData.Data = res.Response[0].ResponseText;
            this.msMsgData.Header = "BatchMaster";
            this.msMsgData.mbBulkDeleteView = true;
            this.mbBulkDeleteView = true;
            this.mbShowDeleteDialog = false;
            this.mbBulkDelete = false;
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText);
          }
        }
      });

    } else {

      if (dataItem.Status != 'NEW') {
        this.msMsgData.Data = this.objUtil.bmgfonTranslate('PAYMENTRANGEPROCESSING.MSG.POSTSTATSCHECK');
        this.msMsgData.Header = "BatchMaster";
        this.msMsgData.mbBulkDeleteView = true;
        this.mbBulkDeleteView = true;
        return;
      }


      this.mObjModel.DocNo = dataItem.DocNo;

      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            DocNo: this.mObjModel.DocNo
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };

      this.PaymentRangeProcessingService.bmlfonPostPaymentRangeProcess(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          //this.msReport = res.Response[0].ResponseText;
          //this.mbReportDialog = true;
          this.msMsgData.Data = res.Response[0].ResponseText;
          this.msMsgData.Header = "BatchMaster";
          this.msMsgData.mbBulkDeleteView = true;
          this.mbBulkDeleteView = true;
          this.getDataList();
        } else {
          if (res.Response[0].ResponseText.length > 80) {
            //this.msReport = res.Response[0].ResponseText;
            //this.mbReportDialog = true;
            this.msMsgData.Data = res.Response[0].ResponseText;
            this.msMsgData.Header = "BatchMaster";
            this.msMsgData.mbBulkDeleteView = true;
            this.mbBulkDeleteView = true;
            this.mbShowDeleteDialog = false;
            this.mbBulkDelete = false;
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText);
          }
        }
      });
    }

  }
  //--------------------------------------------------------------------------------------------
  printReport() {

  }

  bmlFilterSetting() {
    this.filterEnabled = !this.filterEnabled;
  }

 
}
