  public msTemplateId: string = ""
  public mObjDetailModel: ProcessApplyPaymentDetailsModel = new ProcessApplyPaymentDetailsModel()
  public mObjDetailModelBlank: ProcessApplyPaymentDetailsModel = new ProcessApplyPaymentDetailsModel()

  public mbDisabledPeymentLineNo: boolean = false
  public msDescription: string = ""
  public mbShowDialog: boolean = false
  public mbloaderEnabled: boolean = false
  public mbShowDeleteDialog: boolean = false
  public msCallFor: string = ""
  public mbIsLoading: boolean = false
  public marrLookupGridData: any = []
  public mobjLookupKeyData: any = {}
  public mnumLineItemRowIndex: any = null
  public mbIsLookupModal: boolean = false
  public gridDataList = []
  public msCopyRecord: string = "N"
  public msScreenName: string = 'Apply Payment'
  public msDocumentType: string = "0"
  public msReportDialog: string = ""
