  public bmlfonValidateFormData(): boolean {
    let pbFlag: boolean = true
    let pbSelectedRow: boolean = false
    let pbTempFlag: boolean = false
    let parrCheckedRow = this.mArrGrid.filter(x => x.Sel)
    this.mArrGrid.forEach(ele => {
      if (ele.Sel) {
        pbSelectedRow = true;
        return
      }
    })
    /* Abhay/06-July-2020/Bug# 140980 */
    if (this.isEmpty(this.mObjModel.CurrencyKey)) {
      this.objToastService.notifyError(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key1'))
      document.getElementById('txtCurrencyKey').focus()
      pbFlag = false
    } else if (!CustomValidator.bmgfCompareTwoDates(this.mdtPaymentDate, this.mdtApplyDate)) {
      if (this.mObjModel.OptionPayment == true) {
        this.objToastService.notifyError(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key12'))
        pbFlag = false
      } else {
        this.objToastService.notifyError(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key13'))
        pbFlag = false
      }
    } else if (!pbSelectedRow) {
      this.objToastService.notifyError(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key10'))
      pbFlag = false
    } else if (this.isEmpty(this.mObjModel.Vend_CustKey)) {
      this.objToastService.notifyError(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key'))
      document.getElementById('txtVendorCustomer').focus()
      pbFlag = false
    } else if (this.mArrGrid.length == 0) {
      this.objToastService.notifyError(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key8'))
      pbFlag = false
    } else if (this.mObjModel.TotalAppliedAmount > this.mObjModel.RemainingAmount) {
      this.objToastService.notifyError(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key9'))
      pbFlag = false
    } else if (this.mArrGrid.length > 0) {
      this.mArrGrid.forEach(ele => {
        /* Abhay/07-July-2020/Bug#141110 */
        if (ele.WriteOff + ele.ApplyAmount > ele.RemAmount && !pbTempFlag) {
          this.objToastService.notifyError(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key11'))
          pbFlag = false
          pbTempFlag = true
        }
        if (ele.WriteOff > 0 && !pbTempFlag) {
          if (this.isEmpty(this.mObjModel.BatchSeries)) {
            this.objToastService.notifyError(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key5'))
            pbFlag = false
            pbTempFlag = true
          }
        }
      })
    }
    return pbFlag
  }

