  bmlfNgChanges(piRowId, psField) {

    this.miSelectedRowIndex = piRowId
    if (psField == "ApplyAmount") {
      let pnTempApplyAmount: number = 0
      if (this.mArrGrid[piRowId].ApplyAmount > this.mArrGrid[piRowId].RemAmount) {
        this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key4'))
        this.mArrGrid[piRowId].ApplyAmount = 0
      }
      this.mArrGrid.forEach(ele => { pnTempApplyAmount += ele.ApplyAmount })
      this.mObjModel.TotalAppliedAmount = pnTempApplyAmount
      if (Number(this.mArrGrid[piRowId].ApplyAmount) > 0) {
        this.mArrGrid[piRowId].Select = true
      }
      if (this.mbTCSApplicable) {
        let result = (this.mArrGrid[piRowId].TCSDUE / this.mArrGrid[piRowId].RemAmount) * this.mArrGrid[piRowId].ApplyAmount
        if (result <= this.mArrGrid[piRowId].TCSDUE) {
          this.mArrGrid[piRowId].TCSPAID = result
        }
        else {
          this.mArrGrid[piRowId].TCSPAID = this.mArrGrid[piRowId].TCSDUE
        }
      }
    } else if (psField == "WriteOff") {
      let pnTempWriteOff: number = 0
      if (this.mArrGrid[piRowId].WriteOff > this.mArrGrid[piRowId].RemAmount) {
        this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('ApplyPayment.Msg.Key3'))
        this.mArrGrid[piRowId].WriteOff = 0
      }
      this.mArrGrid.forEach(ele => { pnTempWriteOff += ele.WriteOff })
      this.mObjModel.TotalWriteOffAmount = pnTempWriteOff
      if (Number(this.mArrGrid[piRowId].WriteOff) > 0) {
        this.mArrGrid[piRowId].Select = true
      }
    } else if (psField == "Sel") {
      let pnTempApplyAmount: number = 0
      if (this.mArrGrid[piRowId].Sel == true) {
        this.mArrGrid[piRowId].ApplyAmount = this.mArrGrid[piRowId].RemAmount
      } else {
        this.mArrGrid[piRowId].ApplyAmount = 0
        this.mArrGrid[piRowId].WriteOff = 0
        this.mArrGrid[piRowId].TCSPAID = 0
      }
      this.mArrGrid.forEach(ele => { pnTempApplyAmount += ele.ApplyAmount })
      this.mObjModel.TotalAppliedAmount = pnTempApplyAmount
      if (this.mbTCSApplicable) {
        if (this.mArrGrid[piRowId].Sel == true) {
          this.mArrGrid[piRowId].TCSPAID = this.mArrGrid[piRowId].TCSDUE
        }
        else {
          this.mArrGrid[piRowId].TCSPAID = 0
        }
        this.sumTCSApply()
      }
    }

    if ((this.mArrGrid[piRowId].ApplyAmount == 0 || this.mArrGrid[piRowId].ApplyAmount == undefined) && (this.mArrGrid[piRowId].WriteOff == 0 || this.mArrGrid[piRowId].WriteOff == undefined)) {
      this.mArrGrid[piRowId].Sel = false
    } else {
      // this.mArrGrid[piRowId].ApplyAmount = this.mArrGrid[piRowId].RemAmount;
      this.mArrGrid[piRowId].Sel = true
    }
  }

