  public bmlfonAutoApply(): void {
    let pnTempApplyAmout: number = 0
    let pnTempRemainingAmount: number = this.mObjModel.RemainingAmount
    if (this.mArrGrid.length > 0 && pnTempRemainingAmount > 0) {
      this.mArrGrid.forEach(ele => {
        if (ele.RemAmount > pnTempRemainingAmount) {
          ele.ApplyAmount = pnTempRemainingAmount
          pnTempRemainingAmount = 0
        } else {
          ele.ApplyAmount = ele.RemAmount
          pnTempRemainingAmount = pnTempRemainingAmount - ele.RemAmount
        }
        pnTempApplyAmout += ele.ApplyAmount
        if ((ele.ApplyAmount == 0 || ele.ApplyAmount == undefined) && (ele.WriteOff == 0 || ele.WriteOff == undefined)) {
          ele.Sel = false
        } else {
          ele.Sel = true
        }
      })
      this.mObjModel.TotalAppliedAmount = pnTempApplyAmout
    }

    if (this.mbTCSApplicable) {

      let pnTempTCSApplyAmout: number = 0
      let pnTempTCSRemainingAmount: number = this.unappliedTCS // 17/mar/21 scr no.152062
      if (this.mArrGrid.length > 0 && pnTempTCSRemainingAmount > 0) {
        this.mArrGrid.forEach(ele => {
          if (ele.TCSDUE > pnTempTCSRemainingAmount) {
            ele.TCSPAID = pnTempTCSRemainingAmount
            pnTempTCSRemainingAmount = 0
          } else {
            ele.TCSPAID = ele.TCSDUE
            pnTempTCSRemainingAmount = pnTempTCSRemainingAmount - ele.TCSDUE
          }
          pnTempTCSApplyAmout += ele.TCSDUE
        })
        this.mObjModel.TotalAppliedAmount = pnTempTCSApplyAmout
      }
      this.sumTCSApply()
    }
  }

