  //---------------------------------------------------------------------------------------
  //---------------------------------------------------------------------------------------
  bmlfGetDataOnLoad(msVendorDebitNo, msVouchNo) {

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          TicketNo: msVendorDebitNo
        }],
        ScreenName: [{
          CallType: this.msscreenName
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfonGetAPVoucher(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res.HeaderData.length > 0) {
        this.msFormMode = "U";
        this.mbDisabledPrimaryKey = true;
        this.mObjModel = res.HeaderData[0];
        this.mbCustomControl = true;

        this.mObjModel.VoucherDate = new Date(res.HeaderData[0].VoucherDate);
        this.mObjModel.Invdate = new Date(res.HeaderData[0].Invdate);
        this.mnSubTotal = Number(res.HeaderData[0].Vouchtotal) - Number(res.HeaderData[0].TxClc);
        this.msCurrencyTotalLabel = '(' + this.mObjModel.Currency_Key + ')';
        this.mObjModel.Applysysdocid = this.mObjModel.Applysysdocid ? this.mObjModel.Applysysdocid : 0;
        this.mObjModel.Sysdocid = this.mObjModel.Sysdocid ? this.mObjModel.Sysdocid : 0;

        if (this.mObjModel.UpdatedtoFin == '0') {
          this.msStatus = 'NEW';
          this.mbDiabledAllFields = true;
        } else if (this.mObjModel.UpdatedtoFin == '2') {
          this.msStatus = 'POSTED';
          this.mbDiabledAllFields = true;
        } if (this.mObjModel.UpdatedtoFin == '3') {
          this.msStatus = 'VOID';
          this.mbDiabledAllFields = true;
        }
        if (res.DetailData.length > 0) {
          let parrGridData = res.DetailData;
          parrGridData.forEach(ele => {
            //ele.DueDate = new Date(ele.DueDate);
            ele.Masking = '220200';
            ele.AddModify = 'M';
            if (ele.TaxIncInPr == '') {
              ele.TaxIncInPr = 0;
            }
          })

          this.mArrVendorDebitGrid = parrGridData; //res.DetailData;
        }

        this.bmlfHandleControls(this.mObjModel.UpdatedtoFin);//Disable Enable on Status
        this.bmlfGetLookupDes(this.mObjModel);
        this.bmlfGetTDSRate(res.HeaderData[0].TDSType);
        //this.bmlfCalculateTotal(false);//scr145204
        //this.bmlfCalculateTotal(true); //scr 162528
        this.bmlfCalculateTotal(false);
        this.bmlfCheckEsignApplicableOnForm();
      } else {
        this.msFormMode = "I";
        this.mbDisabledPrimaryKey = false;
      }

      if (res.TaxData.length > 0) {//for Vat Tax
        this.mArrTaxData = res.TaxData;
      }

      if (this.msCopyRecord == "Y") {
        this.bmlfonSetFormMode("I", "Copy");
        setTimeout(function () { document.getElementById('txtTicketno').focus(); }, 10);
      }
      if (this.msCallFor == "Post") {
        this.bmlfVoucherPost1();
        return
      }
    })
    this.mbIsLoading = false;
    this.bmlfonURL(this.msDebitNoteNo, msVouchNo)
  }
  //---------------------------------------------------------------------------------------
  bmlfonURL(psKey, psVouchNo) {
    if (psKey == "") {
      this.msURL = "/main/accountspayable/vendor-debit-note/manage";
    } else {
      this.msURL = "/main/accountspayable/vendor-debit-note/manage?DebitNoteNo=" + psKey + '&Vouchno=' + psVouchNo;
    };

  }
