  //---------------------------------------------------------------------------------------
  bmlfGetInputDesColumn(psField): string {
    if (psField == 'Debit Note') {
      return 'Ticketno';
    } else if (psField == 'Finance Series') {
      return 'Ticketno';
    } else if (psField == 'Vendor') {
      return 'Vendkey';
    } else if (psField == 'Ship To') {
      return 'Branchkey';
    } else if (psField == 'Purchase Order') {
      return 'Pono';
    } else if (psField == 'AP Voucher') {
      return 'Ticketno';
    } else if (psField == 'Debit Note') {
      return 'Invno';
    } else if (psField == 'Currency') {
      return 'Currency_Key';
    } else if (psField == 'TDS Type') {
      return 'TDSType';
    } else if (psField == '1099 Type') {
      return 'Type1099';
    } else if (psField == '1099 Box') {
      return 'Box1099';
    }

  }
  //---------------------------------------------------------------------------------------
  bmlfonValidateFormData() {
    if (this.mObjModel.Ticketno.trim() == "") {
      if (this.mbSearchEvent == true) {
        return false;
      }
      if (this.msscreenName == 'AP Voucher') {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.SELECTSERIESAP')));
      } else {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.SELECTSERIES')));
      }

      setTimeout(function () { document.getElementById('txtTicketno').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.VoucherDate == null) {
      //
      if (this.msscreenName == 'AP Voucher') {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.CREDITNOTEDATECHK')));
      } else {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.DEBITNOTEDATECHK')));
      }

      setTimeout(function () { document.getElementById('dateVoucherDate').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.Vendkey == "" && this.mObjModel.Branchkey == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.VENDSHIPCHECK')));
      setTimeout(function () { document.getElementById('txtVendkey').focus(); }, 10);
      return false;
    } else {
      if (this.mObjModel.Vendkey == "") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.VENDORCHECK')));
        setTimeout(function () { document.getElementById('txtVendkey').focus(); }, 10);
        return false;
      } else if (this.mObjModel.Branchkey == "") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.SHIPTOKEYCHECK')));
        setTimeout(function () { document.getElementById('txtVendkey').focus(); }, 10);
        return false;
      }
    }

    if (this.mObjModel.VoucherDate == null) {
      if (this.msscreenName == 'AP Voucher') {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.VOUCHERDATECHK')));
      } else {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.DEBITDATECHK')));
      }

      setTimeout(function () { document.getElementById('dateVoucherDate').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.Vouchtotal == 0) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.ZEROTOTALAMOUNT')));
      return false;
    }
    //--------------Grid Validation-----------------
    let errRow: string = '';
    let errFiled: string = '';
    if (this.mArrVendorDebitGrid.length > 0) {
      this.mArrVendorDebitGrid.forEach(ele => {
        if (ele.errFiled != '') {
          if (ele.QtyOrder == 0) {
            errRow += '' + errRow;
            errFiled = 'QtyOrder';
          }
          if (ele.Description == '') {
            errRow += '' + errRow;
            errFiled = 'Description';
          }
        }
      });

      if (errFiled == "Description") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.ROWENTRYBLANK')));
        return false;
      } else if (errFiled == "QtyOrder") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.ZEROROWAMOUNT')));
        return false;
      }
    }
    //--------------Grid Validation-----------------

    if (this.mObjModel.Amount1099 > this.mObjModel.Vouchtotal) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.AMOUNT1099CHK')));
      return false;
    }

    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.objToastService.notifyWarning(resultString.msg);
      return false;
    }

    return true;
  }
