  //---------------------------------------------------------------------------------------
  bmlfonAddGridLine() {
    //validation
    if (this.mObjModel.Vendkey == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.VENDORCHECK')));
      return false;
    }

    //--------------Grid Validation-----------------
    let errRow: string = '';
    let errFiled: string = '';
    if (this.mArrVendorDebitGrid.length > 0) {
      this.mArrVendorDebitGrid.forEach(ele => {
        if (ele.errFiled != '') {
          if (ele.QtyOrder == 0) {
            errRow += '' + errRow;
            errFiled = 'QtyOrder';
          }
          if (ele.Description == '') {
            errRow += '' + errRow;
            errFiled = 'Description';
          }
        }
      });

      if (errFiled == "Description") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.ROWENTRYBLANK')));
        return false;
      } else if (errFiled == "QtyOrder") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('VENDORDEBITNOTE.MSG.ZEROROWAMOUNT')));
        return false;
      }
    }
    //--------------Grid Validation-----------------
    //--------------End VAlidation------------
    const pobjDataCopy: any = Object.assign({}, this.mObjModelGrid1);

    //----------Row No - S
    let piLastIndex;

    if (this.mArrVendorDebitGrid.length > 0) {
      piLastIndex = this.mArrVendorDebitGrid.reduce((max, obj) => obj.POLinNO > max ? obj.POLinNO : max, this.mArrVendorDebitGrid[0].POLinNO);
    } else {
      piLastIndex = this.mArrVendorDebitGrid.length;
    }

    pobjDataCopy.POLinNO = Number(piLastIndex) + 1;
    pobjDataCopy.DispSeqNo = pobjDataCopy.POLinNO;

    //----------Row No - E
    pobjDataCopy.AddModify = 'A';
    pobjDataCopy.TDSType = this.mObjModel.TDSType
    pobjDataCopy.TDSRate = this.mnTempTDSRate;

    if (pobjDataCopy.TDSRate != 0) {
      pobjDataCopy.TDSAmt = pobjDataCopy.Extension * (pobjDataCopy.TDSRate / 100);
    } else {
      pobjDataCopy.TDSAmt = 0;
    }


    this.mArrVendorDebitGrid.push(pobjDataCopy);
  }
