  //Action Delete line
  bmlfdialogDeleteLineAction(action: any) {

    if (action == 'Yes') {
      this.bmlfonDeleteGrid1Line();
      this.mbShowdeleteLineItem = false;
    }
    if (action == 'No') {
      this.mbShowdeleteLineItem = false;
    }
  }
  //---------------------------------------------------------------------------------------
  bmlfonDeleteGrid1Line() {

    //this.mbChange = true;

    if (this.mArrVendorDebitGrid[this.miGrid1DeleteLineIndex].AddModify == 'A') {
      this.mArrVendorDebitGrid.splice(this.miGrid1DeleteLineIndex, 1)
      this.bmlfCalculateTotal(true);
    } else {
      let reqData: any = {
        JSON: JSON.stringify({
          DetailData: [{
            TicketNo: this.mObjModel.Ticketno,
            RowNo: this.mArrVendorDebitGrid[this.miGrid1DeleteLineIndex].POLinNO
          }],
          ScreenName: [{
            CallType: this.msscreenName
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.mbIsLoading = true;
      this.objService.bmlfonDeleteLineAPVoucher(reqData).subscribe(res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.mArrVendorDebitGrid.splice(this.miGrid1DeleteLineIndex, 1)
          this.bmlfGetDataOnLoad(this.mObjModel.Ticketno, this.mObjModel.Vouchno);//scr 145204
          //this.bmlfCalculateTotal(true); //scr 162528
        }
      });
      this.mbIsLoading = false;
    }// End Else


  }
  //---------------------------------------------------------------------------------------
