  //---------------------------------------------------------------------------------------
  bmlfAmountChanges(rIndex, iField) {
    let TDSValue: any = ''
    //Call GetItemBasePriceService --S

    //Issue No. 175349 -RD-21Jun23 - commnet if conditon
    // if(this.mArrVendorDebitGrid[rIndex]['TDSAmt'] > this.mArrVendorDebitGrid[rIndex]['LineNet']){
    // this.mArrVendorDebitGrid[rIndex]['TDSAmt'] = this.mArrVendorDebitGrid[rIndex]['Extension'] * (this.mArrVendorDebitGrid[rIndex]['TDSRate'] / 100);
    // }//Issue No. 175349 -RD-21Jun23

    this.mbChange = true;
    // this.mnTDSAmount = this.mArrVendorDebitGrid[rIndex]['TDSAmt']
    // if (this.mArrVendorDebitGrid[rIndex]['TDSRate'] != 0) {
    //   TDSValue = this.mArrVendorDebitGrid[rIndex]['Extension'] * (this.mArrVendorDebitGrid[rIndex]['TDSRate'] / 100);
    //   this.mArrVendorDebitGrid[rIndex]['TDSAmt']  = TDSValue
    // } else {
    //   this.mArrVendorDebitGrid[rIndex]['TDSAmt'] = 0;
    // }


    let parrLineItemRow = [];
    let parrDataList = this.mArrVendorDebitGrid.map(x => Object.assign({}, x));
    parrDataList.map(ele => {
      if (ele.POLinNO == this.mArrVendorDebitGrid[rIndex]['POLinNO']) {
        ele.NONINVITEMKEY = "";//ele.Description; DESCRIPTION COMMENTS ACCORDING TO MAHESH
        ele.QtyRem = Number(ele.QtyOrder).toFixed(this.miQtyPrecision);
        ele.QtyOrder = Number(ele.QtyOrder).toFixed(this.miQtyPrecision);

        ele.PriceUOM = ele.PO_Unit;
        ele.BasePrice = Number(ele.BasePrice).toFixed(this.miAmountPrecision);//scr 143766
        ele.DispBasicPrice = Number(ele.BasePrice).toFixed(this.miAmountPrecision);
        ele.Price = Number(ele.BasePrice).toFixed(this.miAmountPrecision);
        ele.TaxAmount = Number(ele.TaxAmount).toFixed(this.miAmountPrecision);//.toFixed(0.0)
        if (ele.TxClc == "NaN" || Number.isNaN(ele.TxClc) || ele.TxClcL == undefined) { ele.TxClc = 0 }
        ele.TxClc = Number(ele.TxClc).toFixed(this.miAmountPrecision);
        if (ele.Vouchtotal == "NaN" || Number.isNaN(ele.Vouchtotal) || ele.Vouchtotal == undefined) { ele.Vouchtotal = 0 }
        ele.Vouchtotal = Number(ele.Vouchtotal).toFixed(this.miAmountPrecision);
        ele.TaxRate = Number(ele.TaxRate).toFixed(this.miAmountPrecision);
        ele.Tax1 = Number(ele.Tax1).toFixed(this.miAmountPrecision);
        ele.Tax2 = Number(ele.Tax2).toFixed(this.miAmountPrecision);
        ele.Tax3 = Number(ele.Tax3).toFixed(this.miAmountPrecision);
        ele.Tax4 = Number(ele.Tax4).toFixed(this.miAmountPrecision);
        ele.TxClcL1 = Number(ele.TxClcL1).toFixed(this.miAmountPrecision);
        ele.TxClcL2 = Number(ele.TxClcL2).toFixed(this.miAmountPrecision);
        ele.TxClcL3 = Number(ele.TxClcL3).toFixed(this.miAmountPrecision);
        ele.TxClcL4 = Number(ele.TxClcL4).toFixed(this.miAmountPrecision);
        ele.Extension = Number(ele.Extension).toFixed(this.miAmountPrecision);
        ele.LineNet = Number(ele.LineNet).toFixed(this.miAmountPrecision);
        ele.TDSRate = Number(ele.TDSRate).toFixed(this.miAmountPrecision);
        ele.TDSAmt = Number(ele.TDSAmt).toFixed(this.miAmountPrecision);
        //toFixed(this.miAmountPrecision);
        if (ele.TaxIncInPr == null) { ele.TaxIncInPr = 0 }
        parrLineItemRow.push(ele);
      }

    });


    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ ...this.mObjModel }],
        DetailData: parrLineItemRow,
        TaxData: [], ScreenName: [{ "CallType": this.msscreenName }],
        Response: [{ "ResponseText": "", "ErrorCode": "" }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfonGetAPVoucherItemBasePrice(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.objToastService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.mArrVendorDebitGrid[rIndex]['BasePrice'] = Number(res.DetailData[0].BasePrice);
        this.mArrVendorDebitGrid[rIndex]['DispBasicPrice'] = Number(res.DetailData[0].BasePrice);
        this.mArrVendorDebitGrid[rIndex]['Price'] = Number(res.DetailData[0].BasePrice);
        this.mArrVendorDebitGrid[rIndex]['Extension'] = Number(res.DetailData[0].Extension);
        this.mArrVendorDebitGrid[rIndex]['LineNet'] = Number(res.DetailData[0].LineNet);
        this.mArrVendorDebitGrid[rIndex]['TaxAmount'] = Number(res.DetailData[0].TaxAmount);
        if (this.msAppTaxType == 'VAT') {

          let piRow: number = this.mArrVendorDebitGrid[rIndex].POLinNO;
          for (let pi = this.mArrTaxData.length - 1; pi >= 0; pi--) {
            if (this.mArrTaxData[pi].DocLinNo.toString() == piRow.toString()) {
              this.mArrTaxData.splice(pi, 1);//Delete exists Tax line
            }
          }
          if (res.TaxData.length > 0) {//Add Tax line
            res.TaxData.forEach(ele => {
              this.mArrTaxData.push(ele);
            })
          };
        }

        this.mArrVendorDebitGrid[rIndex]['TDSAmt'] = Math.ceil(this.mArrVendorDebitGrid[rIndex]['Extension'] * (this.mArrVendorDebitGrid[rIndex]['TDSRate'] / 100));
        this.bmlfCalculateTotal(true);
      }
    });
    this.mbIsLoading = false;
  }
  //---------------------------------------------------------------------------------------
  bmlfCalculateTotal(flg) {
    this.mObjModel.Vouchtotal = Number(0.0);
    this.mnSubTotal = Number(0.0);
    this.mObjModel.TxClc = Number(0);
    this.mnTDSAmount = Number(0.0);
    this.mArrVendorDebitGrid.forEach(ele => {
      this.mObjModel.Vouchtotal = Number(this.mObjModel.Vouchtotal) + Number(ele.LineNet);
      if (ele.TDSAmt == undefined) { ele.TDSAmt = 0 };
      this.mnTDSAmount = Number(this.mnTDSAmount) + Number(ele.TDSAmt);
      this.mObjModel.TxClc = Number(this.mObjModel.TxClc) + Number(ele.TaxAmount);
      //if(ele.TaxIncInPr == "1")
    });
    this.mnSubTotal = Number(this.mObjModel.Vouchtotal) - Number(this.mObjModel.TxClc);
    if (flg == true && this.mObjModel.Type1099 != 0) {
      this.mObjModel.Amount1099 = Number(this.mObjModel.Vouchtotal);
    }
  }
