  //---------------------------------------------------------------------------------------
  // Get Lookup Description
  bmlfGetLookupDes(pobjData) {
    let parrSetKeyData = [
      { KeyCode: 'VENDKEY', KeyValue1: pobjData.Vendkey, KeyValue2: '', KeyDescription: '' },
      { KeyCode: 'BranchKey', KeyValue1: pobjData.Branchkey, KeyValue2: '', KeyDescription: '' },
      { KeyCode: 'COUNTRY', KeyValue1: pobjData.Vendcntry, KeyValue2: '', KeyDescription: '' },
      { KeyCode: 'VendClassKey', KeyValue1: pobjData.Vendclasskey, KeyValue2: '', KeyDescription: '' },
      { KeyCode: 'COMMENTS', KeyValue1: pobjData.Reference, KeyValue2: '', KeyDescription: '' },
      { KeyCode: 'TERMS', KeyValue1: pobjData.Termskey, KeyValue2: '', KeyDescription: '' },
      { KeyCode: 'LOCATION', KeyValue1: pobjData.Location, KeyValue2: '', KeyDescription: '' },
      { KeyCode: '1099TYPE', KeyValue1: pobjData.Type1099, KeyValue2: '', KeyDescription: '' },
      { KeyCode: '1099BOX', KeyValue1: pobjData.Box1099, KeyValue2: pobjData.Type1099, KeyDescription: '' }

    ]
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: parrSetKeyData,
        ScreenName: [{ ScreenName: this.msscreenName }],
        Response: [{ ResponseText: '', ErrorCode: '' }]
      })
    };

    this.userService.bmlfFetchKeyDescription(reqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.LookupData.length) {
        res.LookupData.forEach((ele) => {
          if (ele.KeyCode == 'VENDKEY') {
            this.mObjModel.Vendname = ele.KeyDescription;
          } else if (ele.KeyCode == 'BranchKey') {
            this.msShipToDesc = ele.KeyDescription;
          } else if (ele.KeyCode == 'COUNTRY') {
            this.msVendorCountryDesc = ele.KeyDescription;
          } else if (ele.KeyCode == 'VendClassKey') {
            this.msVendorClassDesc = ele.KeyDescription;
          } else if (ele.KeyCode == 'COMMENTS') {
            this.msReferenceDesc = ele.KeyDescription;
          } else if (ele.KeyCode == 'TERMS') {
            this.msVendorTermsDesc = ele.KeyDescription;
          } else if (ele.KeyCode == 'LOCATION') {
            this.msVendorLocationDesc = ele.KeyDescription;
          } else if (ele.KeyCode == '1099TYPE') {
            this.msType1099Desc = ele.KeyDescription;
          } else if (ele.KeyCode == '1099BOX') {
            this.msBox1099Desc = ele.KeyDescription;
          }
        });
      }
    }, err => {
      console.log(err);
    });
  }
  //---------------------------------------------------------------------------------
  bmlfonDialogAction(action) {

    if (action == 'No') {
      this.mbShowDialog = false;
      this.mbChange = false;
      if (this.msCallFor == "FormClose") {
        if (this.msscreenName == "Vendor Debit Note") {
          this.router.navigate(['/main/accountspayable/vendor-debit-note']);
        } else if (this.msscreenName == "AP Voucher") {
          this.router.navigate(['/main/accountspayable/apvoucher']);
        }
      }
      else if (this.msCallFor == "Add") {
        this.bmlfonClearControl();
      }
      else if (this.msCallFor == "Copy") {
        this.bmlfonSetFormMode("I", "Copy");
      }
      else if (this.msCallFor == "Search") {
        this.bmlfOpenLookupModal('VendorDebitSearch', 'Vendor Debit Note', 'VendorDebitNoteList', 'click', '', 'custom', 'txtTicketno');
        return;
      } else if (this.msCallFor == "Post") {
        //this.bmlfGetDataOnLoad(this.mObjModel.Ticketno, this.mObjModel.Vouchno);
        this.msCallFor = "";
        this.bmlfGetDataOnLoad(this.msDebitNoteNo, this.msVouchno); //scr 147877
      }
      else {
        return;
      }
    }// if no
    else if (action == 'Yes' && this.msCallFor == "FormClose") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Add") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Copy") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Search") {
      this.msCallFor = "SaveSearch";
      this.bmlfonSubmitData();
      this.mbShowDialog = false;
      //this.bmlfOpenLookupModal('VendorDebitSearch', 'Vendor Debit Note', 'VendorDebitNoteList', 'click', '', 'custom', 'txtTicketno');
      //return;
    } else if (action == 'Yes' && this.msCallFor == 'Post') {
      this.mbChange = true;
      this.mbShowDialog = false;
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes') {
      // call update function
      this.mbChange = false;
      this.mbShowDialog = false;
      this.msFormMode = "U";
      this.mbDisabledPrimaryKey = true;
    }
    else {
      this.mbShowDialog = false;
    }
    //setTimeout(function(){document.getElementById('txtTicketno').focus();},10);
  }
