  //----------------------------------------------------------------------------------
  bmlfGetPrecision() {
    try {
      let data = JSON.parse(localStorage.getItem('RoundingDefaults'));
      data.forEach((ele) => {
        if (ele.Default_Key == 'Inventory') {
          this.miQtyPrecision = ele.Default_Value;
          this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision.toString();

          switch (this.miQtyPrecision) {
            case 2: {
              this.mnMaxQty = "9999999999999";
              break;
            }
            case 3: {
              this.mnMaxQty = "999999999999";
              break;
            }
            case 4: {
              this.mnMaxQty = "99999999999";
              break;
            }
            case 5: {
              this.mnMaxQty = "9999999999";
              break;
            }
            case 6: {
              this.mnMaxQty = "999999999";
              break;
            }
            default: {
              this.mnMaxQty = "9999999999999";
              break;
            }
          }
        }

      });
      //this.miQtyPrecision = data[0]["Default_Value"];
      //this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision.toString()
    }
    catch (e) {
      this.miQtyPrecision = 2;
      this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision.toString()
      this.mnMaxQty = "9999999999999";
    }
    let parrDefaultFormat = JSON.parse(localStorage.getItem("defaultFormats"));
    parrDefaultFormat.forEach(ele => {
      if (ele.Default_Key == "CURR_DECIMAL") {
        this.miAmtPrecision = Number(ele.Default_Value);
        this.miAmtPrecisionFormat = 'n' + this.miAmtPrecision;
      }
    });
  }
