  //Print Report
  bmlfOnProcess() {
    this.mbIsLoading = true;

    let psCriteria: string = 'POSTED';
    if (this.msStatus == 'NEW' || this.msStatus == 'HOLD') {//|| this.msStatus == 'VOID'
      psCriteria = 'UNPOSTED';
    } else {
      psCriteria = 'POSTED';
    }

    let pDefaultReportType = localStorage.getItem('reportType');
    let reportData: any = {
      JSON: JSON.stringify({
        DistReport: [{
          "ReportName": "AP Voucher Report",
          "VendorKeyFrom": this.mObjModel.Vendkey,
          "VendorKeyTo": this.mObjModel.Vendkey,
          "Criteria": psCriteria,
          "APVoucherFrom": this.mObjModel.Ticketno,
          "APVoucherTo": this.mObjModel.Ticketno,
          "DateFrom": moment(this.mObjModel.Invdate).format(this.msServeDateFormat),
          "DateTo": moment(this.mObjModel.Invdate).format(this.msServeDateFormat),
          'ListingFlag': 'false',
          'ListingVariable': 'false'
        }],
        Response: [{ 'ResponseText': '', 'ErrorCode': '', 'Pdfpath': '' }]
      })
    };


    this.formService.GetReportData(reportData).subscribe(res => {

      this.mbIsLoading = false;
      if (res != null) {
        //if (res.Response[0].ResponseText == "Success") {
        if (res.Response[0].ErrorCode == "") {
          //this.pdfSrc = res.Response[0].Pdfpath
          //this.modalservice.open(id);
          let pPDFSrc = res.Response[0].Pdfpath;
          if (pDefaultReportType == 'Print') {
            this.objUtils.bmgfPrintPDF(pPDFSrc);
          } else if (pDefaultReportType == 'NewTab') {
            this.objUtils.bmgfOpenPDFNewTab(pPDFSrc);
          } else {
            this.objUtils.bmgfDownloadFile(pPDFSrc, 'AP Voucher', pDefaultReportType);
          }

        }
        else {
          if (res.Response[0].ErrorCode != '') {

            this.bmlfOpenErrorBlock(res.Response[0].ResponseText) //scr 152829 
          }
          else {
            this.bmlfOpenErrorBlock("No Data Found")
          }
          this.mbIsLoading = false;
        }
      }
    }, error => {
      this.mbIsLoading = false;
    });
  }
