  SetGridColumns(GridName, parrGridCol) {
    this.objUtils.GetJsonSetting(this.msscreenName, this.mobjUserData.UserId, GridName, parrGridCol).then((pArrGridChangedCol: any) => {
      if (pArrGridChangedCol != null && pArrGridChangedCol != undefined) {
        parrGridCol = pArrGridChangedCol
      }
    }, err => {
      console.log(err);
    });
  }
