  bmlfGetTDSRate(psTDSType) {
    let pobjReqData = {
      ScreenName: 'AP Voucher',
      LookUpKey: 'TDSRate',
      Filter1: psTDSType
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mnTempTDSRate = res.LookupData[0].TDSRate;
      }else {
        this.mnTempTDSRate = 0;
      }
    });
  }
